<template>
	<div class="shopdetail">
		<div class="main">
			<div class="logo">
				<img :src="shop.imgUrl">
			</div>
			<h2 class="title">{{ shop.title }}</h2>
			<div class="score">
				<div class="star" v-if="shop.star">
					<span v-for="item in parseInt(shop.star)" class="one"></span>
					<span v-if="parseFloat(shop.star) - parseInt(shop.star) >=0.5 ? 1 : 0" class="half"></span>
					<span v-for="item in (5 - parseInt(shop.star) - (parseFloat(shop.star) - parseInt(shop.star) >=0.5 ? 1 : 0) )" class="zero"></span>
				</div>
				<span class="starinfo">{{ shop.star }}分</span>
			</div>
			<span class="shopinfo">起送 ￥{{ shop.mPrice }} | 配送 ￥{{ shop.dPrice }} | 人均 ¥{{ shop.aPrice }}</span>
			<div class="activity" v-if="shop.activity">
				<ul>
					<li>
						<i style="background-image: url('http://p0.meituan.net/xianfu/f8bc8dffdbc805878aa3801a33f563cd1001.png');"></i>
						<span>{{ shop.activity[0].text }}</span>
					</li>
					<li>
						<i style="background-image: url('http://p1.meituan.net/xianfu/9c997ecce6150671b8459738a26f8bd9767.png');"></i>
						<span>{{ shop.activity[1].text }}</span>
					</li>
					<li>
						<i style="background-image: url('http://p0.meituan.net/xianfu/019d1bbb1310b1531e6af6172c9a5095581.png');"></i>
						<span>{{ shop.activity[2].text }}</span>
					</li>
				</ul>
			</div>
			<div class="ad">
				<h2>商家公告</h2>
				<p>{{ shop.ad }}</p>
			</div>
		</div>
		<div class="close">
			<i class="iconfont icon-close" @click="close"></i>
		</div>
	</div>
</template>

<script>
import {mapGetters} from "vuex"

export default {
	methods: {
		close () {
			this.$emit('closeDetail');
		}
	},
	computed: {
		...mapGetters([
			'shop'
		])
	}
}

</script>

<style scoped>
.shopdetail {
	position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(9, 9, 9, 0.4);
  z-index: 99;
}

.main {
	width: 400px;
	height: 600px;
	position: absolute;
	top: 50%;
  left: 50%;
  transform: translate(-50%, -60%);
  overflow: auto;
  background: #151515;
}

.logo {
	width: 80px;
	height: 80px;
	margin: 30px auto;
}

.logo img {
	width: 100%;
	height: 100%;
}

.title {
	font-size: 20px;
	color: #fff;
	text-align: center;
}

.score {
	display: flex;
	margin-top: 10px;
	justify-content: center;
}

.star {
	display: flex;
	width: 100px;
}

.star span {
	flex: 1;
  width: 18px;
  height: 18px;
  margin-right: 2px;
  background-repeat: no-repeat;
  background-size: 100%;
}

.starinfo {
	display: inline-block;
	font-size: 18px;
	color: #fff;
}

.one {
	background-image: url('../assets/star24_on@2x.png');
}

.half {
	background-image: url('../assets/star24_half@2x.png');
}

.zero {
	background-image: url('../assets/star24_off@2x.png');
}

.shopinfo {
	display: block;
	margin: 16px 0;
	font-size: 20px;
	text-align: center;
	color: #fff;
}

.activity {
	padding: 5px 0;
	border-top: 1px solid #d6d6d6; /*no*/
  border-bottom: 1px solid #d6d6d6; /*no*/
}

.activity ul li {
	margin: 10px;
	color: #fff;
}

.activity ul li i {
	display: inline-block;
	width: 20px;
	height: 20px;
	margin-right: 5px;
	background-size: cover;
	vertical-align: middle;
}

.activity ul li span {
	font-size: 14px;
}

.ad {
	margin-top: 20px;
	color: #fff;
}

.ad h2 {
	font-size: 20px;
	text-align: center;
}

.ad p {
	font-size: 17px;
	margin: 10px;
}

.close {
	position: absolute;
	bottom: 15%;
	left: 50%;
	transform: translateX(-50%);
}

.close i {
	font-size: 50px;
	color: #fff;
}
</style>


