<template>
	<div class="confirm">
		<div class="header">
			<span class="back" @click="goback"><i class="iconfont icon-back"></i></span>
			<span class="title">确认订单</span>
		</div>
		<div class="user-info" v-if="!push_order">
			<span class="tip" @click="TurnToAdd"><i class="iconfont icon-tip"></i>请填写您的收货地址等信息！<i class="iconfont icon-right"></i></span>
		</div>
		<div class="user-info" v-else>
			<div class="address" @click="TurnToAddress">
				<i class="iconfont icon-location"></i>
				<p>{{ address }}</p>
				<i class="iconfont icon-right"></i>
			</div>
			<div class="contact">
				<i class="iconfont icon-tip"></i>
				<span class="contact-info">联系人:<i v-if="male" class="iconfont icon-male"></i><i v-else class="iconfont icon-female"></i>{{ name }}</span>
				<span class="contact-info">联系电话:<span class="phone">{{ number }}</span></span>
			</div>
			<div class="d-time">
				<i class="iconfont icon-clock"></i>
				<span class="time-tip">送达时间 <span class="time">12:00</span></span>
			</div>
		</div>
		<div class="order-info">
			<div class="shop-info">
				<div class="shop-img">
					<img width='52' height='39' :src="order_info.img">
				</div>
				<span class="shop-name">{{ order_info.name }}</span>
				<span class="delivery-icon" style="background-image: url('http://p0.meituan.net/aichequan/019c6ba10f8e79a694c36a474d09d81b2000.png');"></span>
			</div>
			<ul>
				<li class="good-item" v-for="item in good_list" :key="item.id">
					<div class="good-img">
						<img width="105" height='79' :src="item.img">
					</div>
					<div class="right-box">
						<div class="good-info">
							<span class="good-name">{{ item.name }}</span>
							<span class="good-price">¥{{ item.price }}</span>
						</div>
						<span class="good-num">X{{ item.num }}</span>
					</div>
				</li>
			</ul>
			<div class="orther">
				<div class="fee">
					<span class="fee-name">包装费</span>
					<span class="fee-price">¥0</span>
				</div>
				<div class="fee">
					<span class="fee-name">配送费</span>
					<span class="fee-price">¥0</span>
				</div>
			</div>
			<div class="price">
				<span class="discount">已优惠¥0</span>
				<span class="total">小计¥{{ order_info.totalPrice }}</span>
			</div>
		</div>
		<div class="btn">
			<div class="left-part">
				<span class="discount-fee">已优惠¥0</span>
				<span class="total-price">合计¥{{ order_info.totalPrice }}</span>
			</div>
			<div class="submit" @click="pushOrder">
				<span>提交订单</span>
			</div>
		</div>
		<alert-msg :alert_text='alert_text' :alert_show='alert_show' @update="update"></alert-msg>
	</div>
</template>

<script>
import AlertMsg from "../components/alertmsg"
import {mapGetters} from 'vuex'

export default {
	components: {
		AlertMsg
	},
	data () {
		return {
			s_id : '',
			order_info: {},
			good_list: [],
			push_order: false,
			address: '',
			name: '',
			number: '',
			male: false,
			alert_text: '',
			alert_show: false,
		}
	},
	methods: {
		goback () {
			this.$router.push({path:'/shop', query:{id:this.s_id}});
		},

		TurnToAdd () {
			this.$router.push('/add_address')
		},

		TurnToAddress () {
			this.$router.push({path:'/address', query:{name:this.user.name}})
		},

		pushOrder () {
			if (!this.push_order) {
				this.alert_text = '请设置收货地址后再提交订单！';
				this.alert_show = true;
			} else {
				let form_data = new FormData();
				let addressInfo = {};
				addressInfo['address'] = this.address;
				addressInfo['name'] = this.name;
				addressInfo['number'] = this.number;
				if (this.male) {
					addressInfo['sex'] = 'male';
				} else {
					addressInfo['sex'] = 'female';
				}
				form_data.append('username', this.user.name);
				form_data.append('s_id', this.s_id);
				form_data.append('goods', JSON.stringify(this.good_list));
				form_data.append('total', this.order_info.totalPrice);
				form_data.append('addressInfo', JSON.stringify(addressInfo));
				this.$http.post('http://120.79.182.246:5000/api/pushOrder', form_data)
					.then( (res) => {
						let order_id = res.data.data;
						this.$router.push({path:'/pay', query:{id:order_id}});
					})
			}
		},

		update (data) {
			this.alert_show = data;
		},
	},
	computed: {
		...mapGetters([
			'login', 'cartlist', 'user'
		])
	},
	created () {
		if (!this.login) {
			this.$router.push('/login');
		} else {
			if (this.user['r_address']) {
				this.push_order = true;
				let index;
				let default_index;
				let have_default = false;
				for (index in this.user['r_address']) {
					if (this.user['r_address'][index].default) {
						have_default = true;
						default_index = index;
					}
				}
				if (have_default) {
					this.address = this.user['r_address'][default_index].address;
					this.name = this.user['r_address'][default_index].name;
					this.number = this.user['r_address'][default_index].number;
					if (this.user['r_address'][default_index].sex === 'male') {
						this.male = true;
					}
				} else {
					this.address = this.user['r_address'][0].address;
					this.name = this.user['r_address'][0].name;
					this.number = this.user['r_address'][0].number;
					if (this.user['r_address'][0].sex === 'male') {
						this.male = true;
					}
				}
			}
			this.s_id = this.$router.history.current.query.id;
			let result = this.cartlist[this.s_id];
			let item;
			for (item in result) {
				if (result[item] instanceof Object) {
					this.good_list.push(result[item]);
				} else {
					if (item !== 'totalNum') {
						this.order_info[item] = result[item];
					}
				}
			}
		}
	}
}
</script>

<style scoped>
.confirm {
	position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  overflow-y: auto;
  background: #f4f4f4;
}

.header {
	position: relative;
	padding: 0 5px;
	text-align: center;
	background: #f4f4f4;
	line-height: 52px;
}

.back {
	position: absolute;
	left: 10px;
}

.back i {
	font-size: 25px;
}

.title {
	flex: 1;
  font-size: 25px; 
  font-weight: 700;
  text-align: center;
}

.user-info {
	background: #fff;
}

.icon-right {
	float: right;
}

.tip {
	display: block;
	height: 60px;
	line-height: 60px;
	font-size: 18px;
	border-bottom: 1px solid #d6d6d6; /*no*/
}

.tip i {
	color: #ffd161;
	font-size: 18px;
	margin: 0 10px;
}

.address, .contact, .d-time {
	display: flex;
	height: 60px;
	line-height: 60px;
	font-size: 18px;
}

.address i {
	color: #ffd161;
	font-size: 18px;
	margin: 0 10px;
}

.contact i {
	color: #ffd161;
	font-size: 18px;
}

.contact .icon-tip {
	margin: 0 10px;
}

.d-time i {
	color: #ffd161;
	font-size: 18px;
	margin: 0 10px;
}

.address {
	align-items: center;
	border-bottom: 1px solid #d6d6d6; /*no*/
}

.address p {
	flex: 1;
	line-height: 30px;
}

.contact {
	border-bottom: 1px solid #d6d6d6; /*no*/
}

.contact-info {
	margin-right: 40px;
}

.phone, .time {
	color: #368ced;
}

.time-tip {
	display: inline-block;
	margin: 0;
}

.order-info {
	background: #fff;
	margin-top: 20px;
}

.shop-info {
	display: flex;
	margin: 0 10px;
	padding: 10px 0;
	align-items: center;
}

.shop-img {
	margin-right: 5px;
}

.shop-name {
	flex: 1;
	font-size: 20px;
	color: #7b7b7b;
}

.delivery-icon {
	width: 70px;
	height: 20px;
	background-size: 100% 100%;
}

.good-item {
	display: flex;
	margin: 10px 10px 0 10px;
	padding-bottom: 10px; 
	background: #fbfbfb;
}

.good-img {
	margin-right: 10px;
}

.right-box {
	flex: 1;
}

.good-info {
	display: flex;
	margin-top: 5px;
	padding-right: 5px;
	justify-content: space-between;
}

.good-name, .good-price {
	font-size: 20px;
	font-weight: 500;
}

.good-num {
	display: block;
	margin-top: 20px;
	font-size: 18px;
	color: #9f9f9f;
}

.orther {
	margin:10px;
}

.fee {
	display:flex;
	justify-content: space-between;
	margin-top: 20px;
	padding-right: 5px;
}

.fee-name, .fee-price {
	font-size: 20px;
}

.price {
	margin: 0 10px 60px;
	padding: 20px 5px 20px 0;
	text-align: right;
	border-top: 1px dashed #999; /*no*/
}

.discount, .total {
	font-size: 20px;
	font-weight: 500;
}

.discount {
	color: #999;
}

.total {
	color: #fb4e44;
	font-weight: 700;
}

.btn {
	position: fixed;
	bottom: 0;
	left: 0;
	display: flex;
	width: 100%;
	height: 60px;
	background: #fff;
}

.left-part {
	flex: 1;
	height: 60px;
	line-height: 60px;
	border-top: 1px solid #e0e0e0; /*no*/
	font-size: 20px;
}

.submit {
	flex: 0 0 160px;
	height: 60px;
	line-height: 60px;
	background: #ffd161;
	text-align: center;
	font-size: 20px;
}

.discount-fee {
	margin-left : 10px;
}

.total-price {
	float: right;
	margin-right: 10px;
	color: #fb4e44;
	font-weight: 700;
}
</style>


