<template>
	<div>
		<span class="decrese" v-show="food.count" @click="removeFood()"><i class="iconfont icon-decrese"></i></span>
		<span class="buy-num" v-show="food.count">{{ food.count }}</span>
		<span class="add"><i class="iconfont icon-add" @click="addFood($event)"></i></span>
	</div>
</template>

<script>
import {mapGetters, mapMutations} from 'vuex'
import Vue from 'vue'

export default {
	props:[
		'food'
	],
	methods:{

		//点击增加按钮
		addFood (event) {
			if (!this.food.count) {
				Vue.set(this.food, 'count', 1)
			} else {
				this.food.count++;
			}
			let info = {
				shop_id: this.shop._id,
				shop_name: this.shop.title,
				shop_img: this.shop.imgUrl,
				good_name: this.food.name,
				good_price: this.food.price,
				good_img: this.food.imgUrl
			};
			this.addGood(info);
			this.$emit('update');
			let el = event.target;
			this.$emit('drop', el);				
		},

		//点击减少按钮
		removeFood () {
			if (this.food.count) {
				this.food.count--;
			}
			let info = {
				shop_id: this.shop._id,
				good_name: this.food.name
			};
			this.removeGood(info);
			this.$emit('update');
		},

		...mapMutations({
			addGood : 'ADD_GOOD',
			removeGood : 'REMOVE_GOOD',
		})
	},
	computed: {
		...mapGetters([
			'shop', 'cartlist'
		])
	}
}

</script>

<style scoped>
.buy-num {
	display: inline-block;
	margin: 0 10px;
	font-size: 16px;
}

.decrese {
	display: inline-block;
	padding: 6px;
	width: 24px;
	height: 24px;
	border: 1px solid #d6d6d6; /*no*/
	border-radius: 50%;
	text-align: center;
}

.add {
	display: inline-block;
	padding: 6px;
	width: 24px;
	height: 24px;
	border-radius: 50%;
	background: #ffd161;
	text-align: center;
}

.decrese i {
	line-height: 24px;
	font-size: 20px;
}

.add i {
	line-height: 24px;
	font-size: 20px;
}
</style>


