<template>
	<div class="sellInfo" v-if="shop">
		<div class="message-info">
			<p class="address"><i class="iconfont icon-location"></i>{{ address }}</p>
			<span class="noline"><i class="iconfont icon-safe"></i>查看食品安全档案</span>
		</div>
		<div class="delivery-info">
			<span class="line"><i class="iconfont icon-form"></i>配送服务:由 商家 提供配送服务</span>
			<span class="noline"><i class="iconfont icon-clock"></i>配送时间:8:00 - 24:00</span>
		</div>
		<div class="activity-info">
			<span class="line"><i class="iconfont icon-soundlight"></i>{{ shop.ad }}</span>
			<span class="line"><i class="iconfont icon-activity"></i>商家活动</span>
			<div class="activity-box" v-if="shop.activity">
				<ul>
					<li>
						<i style="background-image: url('http://p0.meituan.net/xianfu/f8bc8dffdbc805878aa3801a33f563cd1001.png');"></i>
						<span>{{ shop.activity[0].text }}</span>
					</li>
					<li>
						<i style="background-image: url('http://p1.meituan.net/xianfu/9c997ecce6150671b8459738a26f8bd9767.png');"></i>
						<span>{{ shop.activity[1].text }}</span>
					</li>
					<li>
						<i style="background-image: url('http://p0.meituan.net/xianfu/019d1bbb1310b1531e6af6172c9a5095581.png');"></i>
						<span>{{ shop.activity[2].text }}</span>
					</li>
				</ul>
			</div>
		</div>
	</div>
</template>

<script>
import {mapGetters} from "vuex"

export default {
	data () {
		return {
			address: '定位中...',
		}
	},
	computed: {
		...mapGetters([
			'shop'
		])
	},
	watch: {
		shop (data) {
			this.address = this.shop.address;
		}
	},
	created () {
		this.address = this.shop.address;
	}
}
</script>

<style scoped>
.sellInfo {
	position: absolute;
	top: 283px;
	width: 100%;
}

.message-info, .delivery-info, .activity-info {
	width: 100%;
	margin-top: 20px;
	background: #fff;
}

.line {
	border-bottom: 1px solid #d6d6d6; /*no*/
}

.address {
	display: block;
	line-height: 50px;
	height: 50px;
	font-size: 20px;
	border-bottom: 1px solid #d6d6d6; /*no*/
}

.address i {
	line-height: 50px;
	height: 50px;
	font-size: 20px;
	margin: 0 5px;
}

.line, .noline {
	display: block;
	line-height: 50px;
	height: 50px;
	font-size: 20px;
	overflow: hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
}

.line i {
	line-height: 50px;
	height: 50px;
	font-size: 20px;
	margin: 0 5px;
}

.noline i {
	line-height: 50px;
	height: 50px;
	font-size: 20px;
	margin: 0 5px;
}

.activity-box {
	padding: 5px 10px;
}

.activity-box ul li {
	display: flex;
	margin: 10px 0;
}

.activity-box ul li i {
	display: inline-block;
	width: 20px;
	height: 20px;
	margin-right: 8px;
	background-size: cover;
	vertical-align: middle;
}

.activity-box ul li span {
	font-size: 20px;
}
</style>


