<template>
	<div class="typeTab">
		<router-link v-for="(item, key) in typeChoices" :key=item.id class="type" :to="{path:'/category', query:{type:item.shoptype, name:item.text}}">
			<div class="typeImg">
				<img :src="item.imgsrc"></img>
			</div>
			<span class="typeName">{{ item.text }}</span>
		</router-link>
	</div>
</template>

<script>

export default {
	data () {
		return {
			typeChoices: [
				{imgsrc: 'http://p1.meituan.net/jungle/bd3ea637aeaa2fb6120b6938b5e468a13442.png', text: '美食', shoptype: '1'},
				{imgsrc: 'http://p0.meituan.net/94.0.100/jungle/6b93ee96be3df7cf2bb6e661280b047d3975.png', text: '华阳超市', shoptype: '2'},
				{imgsrc: 'http://p0.meituan.net/94.0.100/jungle/f33ed552c52b4466b6308a2c14dbc62d4882.png', text: '生鲜果蔬', shoptype: '3'},
				{imgsrc: 'http://p0.meituan.net/94.0.100/jungle/af6cf63a5dfeb3557bf3099b03002ec34124.png', text: '下午茶', shoptype: '4'},
				{imgsrc: 'http://p1.meituan.net/94.0.100/jungle/1543bbcb048218424e2420a6934e17b24236.png', text: '正餐优选', shoptype: '5'},
				{imgsrc: 'http://p1.meituan.net/94.0.100/jungle/0e63b86b4ff14d214c1999a979fd21d14273.png', text: '汉堡披萨', shoptype: '6'},
				{imgsrc: 'http://p1.meituan.net/94.0.100/jungle/b40b7d72233f7a76dd9d97af40b4b8975414.png', text: '跑腿代购', shoptype: '7'},
				{imgsrc: 'http://p0.meituan.net/94.0.100/jungle/deeea00bb23e4fae31ea154678c7a8003838.png', text: '正餐简餐', shoptype: '8'}
			]
		}
	}
}

</script>

<style scoped>
.typeTab {
	margin: 5px 0;
	display: flex;
	flex-wrap: wrap;
}

.type {
	width: 25%;
	margin: 10px 0;
	display: flex;
	text-align: center;
	flex-direction: column;	
	justify-content: center;
  align-items: center;
  cursor:pointer;
}

.typeImg {
	height: 40px; 
	width: 40px; 
}

.typeImg img {
	height: 100%;
	width: 100%;
}

.typeName {
	margin-top: 5px;
	font-size: 12px;
	color: #000;
}
</style>


