<template>
	<transition name="bounce" @after-enter="afterEnter">
		<div class="alertmsg" v-if="alert_show">
			<span class="msgtext">{{ alert_text }}</span>
		</div>
	</transition>
</template>

<script>

export default {
	props: [
		'alert_text', 'alert_show'
	],
	methods: {
		afterEnter () {
			this.$emit('update', false)
		}
	}
}
</script>

<style scoped>
.alertmsg {
	width: 60vw;
	height: 8vh;
	position: absolute;
	top: 46%;
  left: 20%;
  text-align: center;
  background: #000;
  border-radius: 10px;
  z-index: 99;
}

.msgtext {
	display: block;
	margin: 2vh;
	color: #fff;
	font-size: 20px;
}

.bounce-enter-active {
  animation: bounce-in .5s;
}

.bounce-leave-active {
  animation: bounce-in .5s reverse;
}

@keyframes bounce-in {
  0% {
    transform: scale(0);
  }
  50% {
    transform: scale(1.5);
  }
  100% {
    transform: scale(1);
  }
}
</style>


