<template>
	<div class="pay">
		<div class="header">
			<span class="back" @click="goback"><i class="iconfont icon-back"></i></span>
			<span class="title">支付订单</span>
		</div>
		<div class="img-box">
			<img src="../assets/pay_adv.png">
		</div>
		<div class="time-box">
			<h3>支付剩余时间</h3>
			<span class="timer">{{ minute[0] }}</span>
			<span class="timer">{{ minute[1] }}</span>
			<span class="space">:</span>
			<span class="timer">{{ second[0] }}</span>
			<span class="timer">{{ second[1] }}</span>
		</div>
		<div class="order-info">
			<div class="shop-img">
				<img width="105" height="79" :src="shop.imgUrl">
			</div>
			<div class="right-box">
				<div class="detail-box">
					<span class="shop-name">{{ shop.title }}</span>
					<span class="total-price">总计¥{{ order_info.total_price }}</span>
				</div>
				<div class="msg-box" v-if="order_info.r_address">
					<div class="address">
						<i class="iconfont icon-location"></i>
						<p>收货地址: {{ order_info.r_address.address }}</p>
					</div>
					<div class="contact">
						<i class="iconfont icon-tip"></i>
						<span class="contact-info">联系人:<i v-if="order_info.r_address.sex === 'male'" class="iconfont icon-male"></i><i v-else class="iconfont icon-female"></i>{{ order_info.r_address.name }}</span>
						<span class="contact-info">联系电话:<span class="phone">{{ order_info.r_address.number }}</span></span>
					</div>
				</div>
			</div>
		</div>
		<div class="pay-type">
			<div class="pay-ali">
				<i class="iconfont icon-zhifubaozhifu"></i>
				<span class="pay-name">支付宝支付</span>
				<i class="circle" @click="setAli" :class="{active: pay_type === 1}"></i>
			</div>
			<div class="pay-wx">
				<i class="iconfont icon-weixinzhifu"></i>
				<span class="pay-name">微信支付</span>
				<i class="circle" @click="setWx" :class="{active: pay_type === 2}"></i>
			</div>
		</div>
		<span class="out-btn" @click="showPayBox">确定支付</span>
		<div class="pay-wrapper" v-show="show_Paybox">
			<div class="pay-box">
				<div class="scan">
					<i class="inner-circle" @click="setScan" :class="{active: pay_way === 1}"></i>
					<span>手机扫码支付</span>
				</div>
				<div class="wap">
					<i class="inner-circle" @click="setWap" :class="{active: pay_way === 2}"></i>
					<span>调起APP支付</span>
				</div>
				<span class="submit" @click="TurnToDetail">确定支付</span>
			</div>
			<span class="close" @click="closePaybox"><i class="iconfont icon-close"></i></span>
		</div>
		<alert-msg :alert_text='alert_text' :alert_show='alert_show' @update="update"></alert-msg>
	</div>
</template>

<script>
import AlertMsg from "../components/alertmsg"
import {mapGetters} from 'vuex'

export default {
	components: {
		AlertMsg
	},
	data () {
		return {
			minutes: 15,
      seconds: 0,
      order_id: '',
      order_info: {},
      time_out: false,
      pay_type: 0,
      show_Paybox: false,
      pay_way: 1,
      alert_text: '',
      alert_show: false,
		}
	},
	methods: {
		goback () {
			this.$http.get('http://120.79.182.246:5000/api/deleteOrder?id=' + this.order_id)
				.then( (res) => {
					this.$router.go(-1);
				})
		},

		num (n) {
			return n < 10 ? '0' + n : '' + n;
     },

		add () {
			this.timer = setInterval( () => {
			  if (this.seconds === 0 && this.minutes !== 0) {
			    this.seconds = 59
			    this.minutes -= 1
			  } else if (this.minutes === 0 && this.seconds === 0) {
			    clearInterval(this.timer);
			    this.time_out = true;
			  } else {
			    this.seconds -= 1
			  }
			}, 1000)
    },

    setAli () {
    	this.pay_type = 1;
    },

    setWx () {
    	this.pay_type = 2;
    },

    setScan () {
    	this.pay_way = 1;
    },

    setWap () {
    	this.pay_way = 2;
    },

    update (data) {
    	this.alert_show = data;
    },

    showPayBox () {
    	if (this.time_out) {
    		this.alert_text = '支付超时，请返回上一步重新下单!';
    		this.alert_show = true;
    	} else {
    		if (!this.pay_type) {
    			this.alert_text = '请选择一种支付方式!';
    			this.alert_show = true;
    		} else {
    			this.show_Paybox = true;
    		}
    	}
    },

    closePaybox () {
    	this.show_Paybox = false;
    },

    TurnToDetail () {
    	this.$http.get('http://120.79.182.246:5000/api/editOrder?id=' + this.order_id + '&paytype=' + this.pay_type + '&payway=' + this.pay_way)
    		.then( (res) => {
    			this.$router.push({path:'/finishPay', query:{id:this.order_id}});
    		})
    }
	},
	computed: {
		...mapGetters([
			'shop'
		]),

		second () {
			return this.num(this.seconds);
		},

		minute () {
			return this.num(this.minutes);
		}
	},
	watch: {
		second (data) {
			this.num(data);
		},

		minute (data) {
			this.num(data);
		}
	},
	created () {
		this.order_id = this.$router.history.current.query.id;
		this.$http.get('http://120.79.182.246:5000/api/getOrderInfo?id=' + this.order_id)
			.then( (res) => {
				this.order_info = res.data.data;
				this.add();
			})
	}
}
</script>

<style scoped>
.pay {
	position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  overflow-y: auto;
  background: #f4f4f4;
}

.header {
	position: relative;
	padding: 0 5px;
	text-align: center;
	background: #fff;
	line-height: 52px;
}

.back {
	position: absolute;
	left: 10px;
}

.back i {
	font-size: 25px; 
}

.title {
	flex: 1;
  font-size: 25px;
  font-weight: 700;
  text-align: center;
}

.img-box {
	height: 108px;
	width: 100%;
}

.img-box img {
	width: 100%;
	height: 100%;
}

.time-box {
	margin: 30px 0;
	background: #fff;
	text-align: center;
	padding-bottom: 10px;
}

.time-box h3 {
	font-size: 20px;
	color: #ffd161;
	margin-bottom: 10px;
	padding-top: 10px; 
}

.timer, .space {
	display: inline-block;
	width: 20px;
	height: 20px;
	margin: 5px;
	font-size: 18px;
	background: #ffd161;
	border-radius: 5px;
}

.order-info {
	display: flex;
	padding:10px;
	background: #fff;
}

.shop-img {
	margin-right: 10px;
}

.right-box {
	flex: 1;
}

.detail-box {
	display: flex;
	justify-content: space-between;
	margin-bottom: 5px;
}

.shop-name {
	font-size: 16px;
	font-weight: 700;
}

.total-price {
	font-size: 16px;
	font-weight: 700;
	color: #fb4e44;
}

.address, .contact {
	display: flex;
	margin: 10px 0;
	align-items: center;
}

.address i, .contact i {
	color: #ffd161;
	font-size: 18px;
}

.address i {
	margin-right: 5px;
}

.address p {
	font-size: 16px;
	font-weight: 500;
}

.contact .icon-tip {
	margin-right: 5px;
}

.contact-info {
	font-size: 16px;
	font-weight: 500;
	margin-right: 10px;
}

.phone {
	color: #368ced;
}

.pay-type {
	margin: 30px 0;
	background: #fff;
}

.pay-ali, .pay-wx {
	display: flex;
	padding: 0 20px;
	height: 60px;
	align-items: center;
}

.pay-ali {
	border-bottom: 1px solid #e9e8ea;
}

.pay-ali .iconfont {
	font-size: 26px;
	color: #108ee9;
	margin-right: 30px;
}

.pay-wx .iconfont {
	font-size: 26px;
	color: #62b900;
	margin-right: 30px;
}

.pay-name {
	flex: 1;
	font-size: 28px;
}

.circle {
	display: inline-block;
	width: 28px;
	height: 28px;
	border-radius: 50%;
	border: 1px solid #d6d6d6; /*no*/
}

.active {
	background: #ffd161;
}

.out-btn {
	display: flex;
	margin: 40px 20px 0 20px;
	height: 60px;
	align-items: center;
	justify-content: center;
	background: #ffd161;
	font-size: 28px;
}

.pay-wrapper {
	position: fixed;
	top: 0;
	right: 0;
	left: 0;
	bottom: 0;
	background: rgba(21,17,17,.8);
}

.pay-box {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%,-50%);
	width: 480px;
}

.scan, .wap {
	display: flex;
	margin: 20px 0;
	align-items: center;
	justify-content: center;
}

.inner-circle {
	display: inline-block;
	width: 28px;
	height: 28px;
	margin-right: 10px;
	border-radius: 50%;
	border: 1px solid #d6d6d6; /*no*/
}

.scan span, .wap span {
	color: #fff;
	font-size: 28px;
}

.submit {
	display: flex;
	margin: 40px 20px 0 20px;
	height: 60px;
	align-items: center;
	justify-content: center;
	background: #ffd161;
	font-size: 28px;
}

.close {
	position: absolute;
	top: 80%;
	left: 50%;
	transform: translateX(-50%);
}

.icon-close {
	color: #fff;
	font-size: 30px;
}
</style>


