<template>
	<div class="shop" v-if="shop">
		<div class="topbox">
			<div class="header">
				<span class="back" @click="goback"><i class="iconfont icon-back"></i></span>
				<span class="title">{{ shop.title }}</span>
			</div>
			<div class="shopinfo">
				<div class="logo">
					<img :src="shop.imgUrl">
				</div>
				<div class="detail">
					<span>起送 ￥{{ shop.mPrice }} | 配送 ￥{{ shop.dPrice }} | 人均 ¥{{ shop.aPrice }}</span>
					<p><i class="iconfont icon-soundlight"></i>{{ shop.ad }}</p>
				</div>
			</div>
			<div class="activity" v-if="shop.activity">
				<ul :style=" 'transform: translateY('+ defaultNum % calActivity * -0.961538 +'rem)'">
					<li>
						<i style="background-image: url('http://p0.meituan.net/xianfu/f8bc8dffdbc805878aa3801a33f563cd1001.png');"></i>
						<span>{{ shop.activity[0].text }}</span>
					</li>
					<li>
						<i style="background-image: url('http://p1.meituan.net/xianfu/9c997ecce6150671b8459738a26f8bd9767.png');"></i>
						<span>{{ shop.activity[1].text }}</span>
					</li>
					<li>
						<i style="background-image: url('http://p0.meituan.net/xianfu/019d1bbb1310b1531e6af6172c9a5095581.png');"></i>
						<span>{{ shop.activity[2].text }}</span>
					</li>
				</ul>
				<span class="activity-num" @click="openDeatail">{{ calActivity }}个活动 ></span>
			</div>
		</div>
		<transition name="fade">
			<shop-detail v-show="showDetail" @closeDetail="closeDetail"></shop-detail>
		</transition>
		<div class="tab">
			<router-link :to="{path:'/shop/menu', query:{id: s_id}}"><span>点菜</span></router-link>
			<router-link :to="{path:'/shop/comment', query:{id: s_id}}"><span>评价</span></router-link>
			<router-link :to="{path:'/shop/seller', query:{id: s_id}}"><span>商家</span></router-link>
		</div>
		<keep-alive>
      <router-view></router-view>
    </keep-alive>
	</div>
</template>

<script>
import ShopDetail from '../components/shopdetail'
import {mapGetters, mapMutations} from 'vuex'

export default {
	components: {
		ShopDetail
	},
	data () {
		return {
			defaultNum: 0,
			showDetail: false,
			s_id: '',
		}
	},
	computed: {
		...mapGetters([
			'shop'
		]),
		calActivity () {
			return this.shop.activity ? this.shop.activity.length : 0;
		}
	},
	methods: {
		goback () {
      this.$router.go(-1);
		},
		openDeatail () {
			this.showDetail = true;
		},
		closeDetail () {
			this.showDetail = false;
		},
		...mapMutations({
			setShop : 'SET_SHOP'
		})
	},
	created () {
		this.s_id = this.$router.history.current.query.id;
		this.$http.get('http://120.79.182.246:5000/api/getshop?id=' + this.s_id)
			.then( (res) => {
				this.setShop(res.data.data);
			});
		setInterval(() => {
			this.defaultNum++;
    }, 4000);
	}
}

</script>

<style scoped>
.shop {
	position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: #f4f4f4;
}

.topbox {
	background: #333333;
}

.header {
	position: relative;
	padding: 0 5px;
	text-align: center;
	line-height: 52px;
	color: #fff;
}

.back {
	position: absolute;
	left: 10px;
}

.back i {
	font-size: 25px;
}

.title {
	flex: 1;
  font-size: 25px;
  font-weight: 700;
  text-align: center;
}

.shopinfo {
	display: flex;
	margin-top: 10px;
	padding-bottom: 20px;
	border-bottom: 1px solid #d6d6d6; /*no*/
}

.logo {
	width: 80px;
	height: 80px;
	margin: 0 10px;
}

.logo img {
	width: 100%;
	height: 100%;
}

.detail {
	display: flex;
	flex: 1;
	flex-direction: column;
}

.detail span {
	margin-top: 10px;
	color: #d6d6d6;
	font-size: 16px;
}

.detail p {
	margin-top: 20px;
	width: 300px;
	overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
	color: #d6d6d6;
	font-size: 16px;
}

.detail p i {
	font-size: 18px;
	margin-right: 5px;
}

.activity {
	height: 50px;
	margin: 0 10px;
	position: relative;
	overflow: hidden;
	line-height: 50px;
}

.activity ul {
	transition: all 2s;
}

.activity ul li {
	display: flex;
	align-items: center;
}

.activity ul li i {
	display: inline-block;
	width: 20px;
	height: 20px;
	margin-right: 5px;
	background-size: cover;
}

.activity ul li span {
	display: inline-block;
	font-size: 14px; 
	color: #d6d6d6;
}

.activity-num {
  position: absolute;
  top: 0;
  right: 0;
  font-size: 14px;
  color: #d6d6d6;
}

.fade-enter-active, .fade-leave-active {
	transition: opacity 1s;
}

.fade-enter, .fade-leave-active {
	opacity: 0;
}

.tab {
	display: flex;
	background: #fff;
	line-height: 60px;
}

.tab a {
	flex: 1;
	text-align: center;
	font-size: 20px;
	color: #000;
}

.tab a span {
	display: inline-block;
}


.router-link-active span {
	color: #ffd161;
	margin-bottom: 5px;
	border-bottom: 3px solid #ffd161; /*no*/
}
</style>


