<template>
	<div class="Raddress">
		<div class="header">
			<span class="back" @click="goback"><i class="iconfont icon-back"></i></span>
			<span class="title">收货地址</span>
			<span class="manage" @click="toggleDel">{{ deltext }}</span>
		</div>
		<div class="address-box">
			<ul>
				<li class="address-item" v-for="(item, index) in address_list" :key="item.id">
					<div class="address">
						<i class="iconfont icon-location"></i>
						<p>{{ item.address }}</p>
						<span v-if="item.default && !delFlag" class="default">默认地址</span>
						<span v-if="!delFlag && !item.default" class="set-default" @click="setDefault(item, index)">设为默认地址</span>
						<i v-if="delFlag" class="circle" :class="{active:item.del}" @click="toggoleAttri(item)"></i>
					</div>
					<div class="contact">
						<i class="iconfont icon-tip"></i>
						<span class="contact-info">联系人:<i v-if="item.sex == 'male'" class="iconfont icon-male"></i><i v-else class="iconfont icon-female"></i>{{ item.name }}</span>
						<span class="contact-info">联系电话:<span class="phone">{{ item.number }}</span></span>
					</div>
				</li>
			</ul>
		</div>
		<div class="new-address" v-if="!delFlag" @click="TurnToAdd">
			<span>新增收货地址</span>
		</div>
		<div class="delete-address" v-else @click="delAdd">
			<span>删除</span>
		</div>
		<alert-msg :alert_text='alert_text' :alert_show='alert_show' @update="update"></alert-msg>
	</div>
</template>

<script>
import AlertMsg from "../components/alertmsg"
import Vue from 'vue'
import {mapMutations} from 'vuex'

export default {
	components: {
		AlertMsg
	},
	data () {
		return {
			address_list: [],
			u_name: '',
			delFlag: false,
			deltext: '管理',
			prevent_flag: false, //避免重复请求
			alert_text: '',
			alert_show: false,
		}
	},
	methods: {
		goback () {
			this.$http.get('http://120.79.182.246:5000/api/getuserInfo?username=' + this.u_name)
				.then( (res) => {
					this.updateUser(res.data.data);
					this.$router.go(-1);
				})
		},

		toggleDel () {
			this.delFlag = !this.delFlag;
			if (this.delFlag) {
				this.deltext = '取消';
			} else {
				this.deltext = '管理';
			}
		},

		TurnToAdd () {
			let length = this.address_list.length;
			if (length === 5) {
				this.alert_text = '收货地址请不要超过5个!';
				this.alert_show = true;
			} else {
				this.$router.push('/add_address');
			}
		},

		toggoleAttri (addItem) {
			if (!addItem.del) {
				Vue.set(addItem, 'del', true);
			} else {
				addItem.del = !addItem.del;
			}
		},

		setDefault (addItem, index) {
			if (!this.prevent_flag) {
				this.prevent_flag = true;
				this.$http.get('http://120.79.182.246:5000/api/setdefault?username=' + this.u_name + '&index=' + index)
				.then( (res) => {
					this.address_list = res.data.data.r_address;
					this.prevent_flag = false;
					this.alert_text = '默认地址设置成功!';
					this.alert_show = true;
				})
			} else {
				this.alert_text = '请不要操作太频繁!';
				this.alert_show = true;
			}
		},

		delAdd () {
			let delList = '';
			let delLength;
			let index;
			for (index in this.address_list) {
				if (this.address_list[index].del) {
					delList += index;
				}
			}
			delLength = delList.length;
			if (!delLength) {
				this.alert_text = '请至少选择一个地址';
				this.alert_show = true;
				return;
			} else if (delLength === this.address_list.length) {
				this.alert_text = '请至少保留一个地址';
				this.alert_show = true;
				return;
			} else {
				if (!this.prevent_flag) {
					this.prevent_flag = true;
					this.$http.get('http://120.79.182.246:5000/api/delRaddress?username=' + this.u_name + '&del=' + delList)
					.then( (res) => {
						this.address_list = res.data.data.r_address;
						this.prevent_flag = false;
						this.alert_text = '地址删除成功!';
						this.alert_show = true;
					})
				} else {
					this.alert_text = '请不要操作太频繁!';
					this.alert_show = true;
				}
			}
		},

		update (data) {
			this.alert_show = data;
		},

		...mapMutations ({
			updateUser: 'UPDATE_USER'
		})
	},
	created () {
		this.u_name = this.$router.history.current.query.name;
		this.$http.get('http://120.79.182.246:5000/api/getRaddress?username=' + this.u_name)
			.then( (res) => {
				this.address_list = res.data.data;
			})
	}
}
</script>

<style scoped>
.Raddress {
	position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  overflow-y: auto;
  background: #f4f4f4;
}

.header {
	position: relative;
	padding: 0 5px;
	text-align: center;
	background: #fff;
	line-height: 52px;
}

.back {
	position: absolute;
	left: 10px;
}

.back i {
	font-size: 25px;
}

.title {
	flex: 1;
  font-size: 25px; 
  font-weight: 700;
  text-align: center;
}

.manage {
	position: absolute;
	right: 10px;
	font-size: 25px;
	font-weight: 500;
}

.address-box {
	background: #fff;
	margin-top: 20px;
}

.address-item {
	display: flex;
	margin: 0 10px;
	padding: 10px 0;
	flex-direction: column;
	border-bottom: 1px solid #d6d6d6; /*no*/
}

.address, .contact {
	display: flex;
	align-items: center;
	height: 30px;
	line-height: 30px;
	font-size: 18px;
	margin-top: 20px;
}

.address i {
	color: #ffd161;
	font-size: 20px;
	margin-right: 5px;
}

.address p {
	flex: 1;
}

.default {
	color: #ffd161;
	font-weight: 500;
}

.set-default {
	font-weight: 500;
	cursor:pointer;
}

.circle {
	display: block;
	width: 20px;
	height: 20px;
	margin-right: 5px;
	border: 1px solid #d6d6d6; /*no*/
	border-radius: 50%
}

.active {
	background: #ffd161;
}

.contact i {
	color: #ffd161;
	font-size: 20px;
}

.contact .icon-tip {
	margin-right: 5px;
}

.contact-info {
	margin-right: 20px;
}

.phone {
	color: #368ced;
}

.new-address, .delete-address {
	position: absolute;
	bottom: 0;
	left: 0;
	width: 100%;
	height: 60px;
	line-height: 60px;
	text-align: center;
	font-size: 20px;
}

.new-address {
	background: #ffd161;
}

.delete-address {
	background: #FF0000;
}
</style>


