<template>
	<div class="login">
		<div class="header">
			<span class="back" @click="goback"><i class="iconfont icon-back"></i></span>
			<span class="title">登录</span>
		</div>
		<form class="loginform" @keyup.enter="clickBtn">
			<div class="username">
				<label for="username">账号</label>
				<input v-model="u_name" id="username" type="text" placeholder="请输入用户名" @change="searchUser">
			</div>
			<div class="password">
				<label for="password">密码</label>
				<input v-model="u_pwd" id="password" :type="pwdType[defaultIndex].typename" placeholder="请输入密码">
				<span><i class="iconfont" :class="pwdType[defaultIndex].itype" @click="changeType(defaultIndex)"></i></span>
			</div>
			<div class="button" @click="clickBtn">
				<span>登录</span>
			</div>
			<span class="tip">未注册直接输入账号密码，自动注册！</span>
		</form>
		<alert-msg :alert_text="alert_text" :alert_show="alert_show" @update="update"></alert-msg>
	</div>
</template>

<script>
import AlertMsg from '../components/alertmsg'
import {mapActions, mapGetters} from 'vuex'

export default {
	components: {
		AlertMsg,
	},
	data () {
		return {
			pwdType:[
				{typename: "password", itype: "icon-attention_light"},
				{typename: "text", itype: "icon-attentionforbid"}
			],
			defaultIndex: 0,
			u_name: '',
			u_pwd: '',
			registed: false,
			alert_text: '',
			alert_show: false,
		}
	},
	methods: {
		goback () {
			this.$router.go(-1);
		},
		changeType (index) {
			if (index == 0) {
				this.defaultIndex = 1;
			} else {
				this.defaultIndex = 0;
			}
		},
		searchUser () {
			this.$http.get('http://120.79.182.246:5000/api/getuser?username=' + this.u_name)
				.then( (res) => {
					if (res.data.data == 1) {
						this.registed = true;
					}
				});
		},
		clickBtn () {
			if (this.u_name == '' || this.u_pwd == '') {
				this.alert_text = '用户名或密码不能为空！';
				this.alert_show = true;
				return;
			};
			if (this.registed) {
				this.$http.get('http://120.79.182.246:5000/api/login?username=' + this.u_name + '&password=' + this.u_pwd)
					.then( (res) => {
						if (res.data.code == 0) {
							this.alert_text = '密码错误！请重新输入！';
							this.alert_show = true;
						} else {
							this.loginUser(res.data.data);
							this.goback();
						}
					});
			} else {
				this.$http.get('http://120.79.182.246:5000/api/register?username=' + this.u_name + '&password=' + this.u_pwd)
					.then ( (res) => {
						this.loginUser(res.data.data);
						this.goback();
					});
			}
		},
		update (data) {
			this.alert_show = data;
		},
		...mapActions([
			'loginUser'
		])
	},
	computed: {
		...mapGetters([
			'location'
		])
	},
	created () {
		if (this.location == '') {
			this.$router.push('/index');
		}
	}
}
</script>

<style scoped>
.login {
	position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  overflow-y: auto;
}

.header {
	position: relative;
	padding: 0 5px;
	text-align: center;
	line-height: 52px;
}

.back {
	position: absolute;
	left: 10px;
}

.back i {
	font-size: 25px;
}

.title {
	flex: 1;
  font-size: 25px;
  font-weight: 700;
  text-align: center;
}

.loginform {
	margin-top: 100px;
}

label {
	font-size: 20px;
}

input {
	border: none;
	outline: none;
	padding: 0;
  margin: 0;
  width: 70%;
	margin-left: 40px;
	font-size: 20px;
}

.username {
	margin: 20px 30px;
	border-bottom: 1px solid #e4e4e4; /*no*/
}

.password {
	margin: 20px 30px;
	border-bottom: 1px solid #e4e4e4; /*no*/
}

.password span {
	float: right;
}

.password span i {
	font-size: 40px; /*px*/
}

.button {
	margin: 30px 0; 
	text-align: center;
}

.button span {
	display: block;
	width: 90%;
	margin: 0 5vw;
	line-height: 60px;
	color: #fff;
	background: #ffd161;
  border-radius: 10px;
  font-size: 24px;
}

.tip {
	display: block;
  text-align: center;
  font-size: 20px;
}
</style>


