<template>
	<div class="search">
		<div class="header">
			<span class="back" @click="goback"><i class="iconfont icon-back"></i></span>
			<span class="title">搜索商家</span>
		</div>
		<div class="searchBox">
			<div class="searchInput">
				<i class="iconfont icon-search"></i>
				<input v-model="keywords" type="text" placeholder="请输入店铺名">
			</div>
		</div>
		<div class="searchResult" v-if="s_results">
			<ul>
				<router-link tag="li" :to="{path:'/shop', query:{id:item._id}}" v-for="item in s_results" :key=item.id>
					<span class="shopImg">
						<img :src="item.imgUrl">
					</span>
					<div class="right-box">
						<span class="shopName">{{ item.title }}</span>
						<span class="dl_time">{{ item.dltime }}分钟送达</span>
						<span class="icon"><i class="iconfont icon-right"></i></span>
					</div>
				</router-link>
			</ul>
		</div>
		<alert-msg :alert_text='alert_text' :alert_show='alert_show' @update="update"></alert-msg>
	</div>
</template>

<script>
import AlertMsg from "../components/alertmsg"
import {mapGetters} from "vuex"

export default {
	components: {
		AlertMsg
	},
	data () {
		return {
			s_results: [],
			keywords: '',
			alert_text: '未找到相关店铺！请尝试其他关键字!',
			alert_show: false,
		}
	},
	computed: {
		...mapGetters([
			'location'
		])
	},
	methods: {
		update (status) {
			this.alert_show = status;
		},
		goback () {
			this.$router.go(-1);
		}
	},
	watch: {
		keywords (data) {
			if (data !== "") {
				this.$http.get('http://120.79.182.246:5000/api/searchshops?kw=' + data + '&location=' + this.location)
				.then( (res) => {
					if (res.data.data.length !== 0) {
						this.s_results = res.data.data;
					} else {
						this.alert_show = true;
					}
				});
			} else {
				this.s_results = [];
			}
		}
	}
}
</script>

<style scoped>
.search {
	position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  overflow-y: auto;
  background: #f4f4f4;
}

.header {
	position: relative;
	padding: 0 5px;
	text-align: center;
	background: #fff;
	line-height: 52px;
}

.back {
	position: absolute;
	left: 10px;
}

.back i {
	font-size: 25px;
}

.title {
	flex: 1;
  font-size: 25px;
  font-weight: 700;
  text-align: center;
}

.searchBox {
	display: flex;
  padding-bottom: 10px;
  background: #fff;
  line-height: 40px;
}

.searchInput {
	flex: 1;
	margin: 0 10px;
  padding-left: 10px;
  background: #efeef4;
  border-radius: 20px;
  line-height: 40px;
}

.searchInput input {
  width: 85%;
  border: none;
  text-indent: 10px;
  outline: none;
  background: #efeef8;
  line-height: normal;
  padding: 0;
  margin: 0;
  font-size: 18px;
}

.searchResult ul li {
	display: flex;
  align-items: center;
  background: #fff;
  margin-bottom: 10px;
  line-height: 120px;
}

.shopImg {
	width: 140px;
	height: 100px;
	margin: 0 10px;
}

.shopImg img {
	width: 100%;
	height: 100%;
}

.right-box {
	flex: 1;
	display: flex;
	align-items: center;
}

.shopName {
	flex: 1;
	line-height: 20px;
  font-size: 18px;
  font-weight: 700;
  text-align: center;
}

.dl_time {
	font-size: 14px;
}

</style>


