<template>
	<div class="add">
		<div class="header">
			<span class="back" @click="goback"><i class="iconfont icon-back"></i></span>
			<span class="title">新增收货地址</span>
			<span class="save" @click="saveAddress">保存</span>
		</div>
		<h3 class="type">联系人</h3>
		<div class="user-info">
			<div class="user-name">
				<span class="name">姓名:</span>
				<div class="input-box">
					<input v-model="user_name" type="text" placeholder="请填写收货人姓名">
				</div>
			</div>
			<div class="user-sex">
				<div>
					<i class="circle" @click="setmale" :class="{active:male}"></i>
					<span>先生</span>
				</div>
				<div>
					<i class="circle" @click="setfemale" :class="{active:female}"></i>
					<span>女士</span>
				</div>
			</div>
			<div class="phone-number">
				<span class="number">电话:</span>
				<div class="input-box">
					<input v-model="phone_number" type="text" placeholder="请填写收货人手机号">
				</div>
			</div>
		</div>
		<h3 class="type">收货地址</h3>
		<div class="address-info">
			<div class="location">
				<span class="about">小区/大厦/学校:</span>
				<div class="to-location" @click="toLocation">
					<i class="iconfont icon-location"></i>
					<span>{{ hasLocate }}</span>
					<i class="iconfont icon-right"></i>
				</div>
			</div>
			<div class="house">
				<span class="detail">楼号/门牌号:</span>
				<div class="input-box">
					<input v-model="house_address" type="text" placeholder="例:11栋21楼2101">
				</div>
			</div>
		</div>
		<alert-msg :alert_text='alert_text' :alert_show='alert_show' @update="update"></alert-msg>
	</div>
</template>

<script>
import AlertMsg from "../components/alertmsg"
import {mapGetters, mapMutations} from 'vuex'

export default {
	components: {
		AlertMsg,
	},
	data () {
		return {
			user_name: '',
			phone_number: '',
			choose: false,
			male: false,
			female: false,
			about_address: '',
			house_address: '',
			has_locate: false,
			alert_text: '',
			alert_show: false,
			not_phone: true,
		}
	},
	methods: {
		goback () {
			this.$router.go(-1);
		},

		setmale () {
			this.choose = true;
			this.male = true;
			this.female = false;
		},

		setfemale () {
			this.choose = true;
			this.female = true;
			this.male = false;
		},

		toLocation () {
			this.$router.push({path:'/location', query:{fromAdd:true}});
		},

		saveAddress () {
			if (!this.user_name || !this.phone_number || !this.choose || !this.about_address || !this.house_address) {
				this.alert_text = '信息填写不完整!';
				this.alert_show = true;
				return;
			}
			this.confirmNum();
			if (this.not_phone) {
				return;
			} else {
				let form_data = new FormData();
				let sex = this.male ? 'male' : 'female';
				let address = this.about_address + this.house_address;
				form_data.append('username', this.user.name);
				form_data.append('rname', this.user_name);
				form_data.append('sex', sex);
				form_data.append('number', this.phone_number);
				form_data.append('raddress', address);
				this.$http.post('http://120.79.182.246:5000/api/addReceive', form_data)
					.then( (res) => {
						this.updateUser(res.data.data);
						this.setRAddress('');
						this.user_name = '';
						this.phone_number = '';
						this.choose = false;
						this.male = false;
						this.female = false;
						this.about_address = '';
						this.house_address = '';
						this.has_locate = false;
						this.alert_text = '';
						this.alert_show = false;
						this.not_phone = true;
						this.$router.go(-1);
					})
			}
		},

		confirmNum () {
			let myreg = /^1[3|4|5|7|8][0-9]{9}$/;
			if (!myreg.test(this.phone_number)) {
				this.alert_text = '手机号码格式不正确';
				this.alert_show = true;
			} else {
				this.not_phone = false;
			}
		},

		update (data) {
			this.alert_show = data;
		},

		...mapMutations({
			updateUser: 'UPDATE_USER',
			setRAddress: 'SET_RECEIVE_ADDRESS',
		})
	},
	computed: {
		...mapGetters([
			'login', 'r_address', 'user', 'shop'
		]),

		hasLocate () {
			if (this.r_address) {
				this.has_locate = true;
				this.about_address = this.r_address;
				return this.r_address;
			} else {
				return '点击选择';
			}
		}
	}
}
</script>

<style scoped>
.add {
	position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  overflow-y: auto;
  background: #f4f4f4;
}

.header {
	position: relative;
	padding: 0 5px;
	text-align: center;
	background: #fff;
	line-height: 52px;
}

.back {
	position: absolute;
	left: 10px;
}

.back i {
	font-size: 25px;
}

.title {
	flex: 1;
  font-size: 25px; 
  font-weight: 700;
  text-align: center;
}

.save {
	position: absolute;
	right: 10px;
	font-size: 25px;
	font-weight: 700;
}

h3 {
	margin: 20px 10px;
	font-size: 16px;
	font-weight: 700;
}

.user-info, .address-info {
	background: #fff;
}

.user-name, .user-sex, .phone-number, .location, .house {
	display: flex;
	height: 60px;
	line-height: 60px;
	border-bottom: 1px solid #d6d6d6; /*no*/
}

.name, .number, .about, .detail {
	font-size: 18px;
	font-weight: 700;
	margin-left: 10px; 
}

.input-box {
	display: inline-block;
	flex: 1;
	margin-left: 20px;
}

.input-box input {
	width: 90%;
	line-height: 60px;
	padding: 0;
	font-size: 18px;
	border: none;
	outline: none;
}

.user-sex {
	padding-left: 70px;
}

.user-sex div {
	margin-right: 20px;
	display: flex;
	align-items: center;
}

.circle {
	display: block;
	width: 20px;
	height: 20px;
	margin-right: 5px;
	border: 1px solid #d6d6d6; /*no*/
	border-radius: 50%
}

.active {
	background: #ffd161;
}

.user-sex div .iconfont {
	font-size: 20px;
	margin-right: 5px;
}

.user-sex div span {
	font-size: 18px;
	font-weight: 700;
}

.to-location {
	flex: 1;
	margin-left: 10px;
	color: #9d9d9d;
}

.to-location i {
	font-size: 20px;
}

.to-location span {
	font-size: 20px; 
}

.to-location .icon-right {
	float: right;
	margin-right: 10px;
}
</style>


