<template>
	<div class="comment">
		<div class="header">
			<span class="back" @click="goback"><i class="iconfont icon-back"></i></span>
			<span class="title">评价</span>
		</div>
		<div class="comment-box">
			<div class="shop-info">
				<div class="shop-img">
					<img width="50" height="50" :src="shop_img">
				</div>
				<span class="shop-name">{{ shop_name }}</span>
			</div>
			<div class="type">
				<div class="type-item">
					<span class="type-name">配送评分</span>
					<div class="star">
						<span class="iconfont icon-star" v-for="item in 5" @click="setDrate(item)" :class="{active: item <= d_rate}"></span>
					</div>
					<span class="star-tip">{{ tip_text[d_rate] }}</span>
				</div>
				<div class="type-item">
					<span class="type-name">商家评分</span>
					<div class="star">
						<span class="iconfont icon-star" v-for="item in 5" @click="setSrate(item)" :class="{active: item <= s_rate}"></span>
					</div>
					<span class="star-tip">{{ tip_text[s_rate] }}</span>
				</div>
			</div>
			<div class="input-box">
				<textarea v-model="comment_content" placeholder="亲！菜品口味如何？对包装服务等还满意吗？（写够15字，才是好同志!）"></textarea>
				<span v-show="comment_content.length < 15" class="fifteen-tip">加油，还差{{ 15 - comment_content.length }}字即可发布!</span>
			</div>
			<div class="upload-img">
				<div class="img-box" v-for="(item, index) in img_list">
					<img :src="item">
					<i class="iconfont icon-jian" @click="delImg(item, index)"></i>
				</div>
				<label class="upload-box">
					<i class="iconfont icon-xiangji"></i>
					<input type="file" style="display:none" accept="image/*" @change="uploadImg($event)">
				</label>
				<div class="upload-tip" v-if="!img_list.length">
					<h3>上传图片</h3>
					<span>内容丰富的评论有机会成为优质评价获取奖励哦！</span>
				</div>
			</div>
			<div class="hide-name">
				<i class="circle" @click="toggleHide" :class="{hide:hide_name}"></i>
				<span>匿名评价</span>
			</div>
		</div>
		<span v-if="!canSub" class="cant-sub">提交评价</span>
		<span v-else class="submit" @click="uploadComment">提交评价</span>
		<alert-msg :alert_text="alert_text" :alert_show="alert_show" @update="update"></alert-msg>
	</div>
</template>

<script>
import AlertMsg from '../components/alertmsg'
import {mapGetters} from 'vuex'

export default {
	components: {
		AlertMsg
	},
	data () {
		return {
			order_id: '',
			shop_img: '',
			shop_name: '',
			d_rate: 0,
			s_rate: 0,
			tip_text: ['该项不能为空哦！', '差', '一般', '还不错', '满意', '墙裂推荐'],
			comment_content: '',
			img_list: [],
			loading: false,
			alert_text: '',
			alert_show: false,
			hide_name: false,
		}
	},
	methods: {
		goback () {
			this.$router.go(-1);
		},

		setDrate (item) {
			if (this.d_rate === item) {
				this.d_rate = 0;
			} else {
				this.d_rate = item;
			}
		},

		setSrate (item) {
			if (this.s_rate === item) {
				this.s_rate = 0;
			} else {
				this.s_rate = item;
			}
		},

		update (data) {
			this.alert_show = data;
		},

		toggleHide () {
			this.hide_name = !this.hide_name;
		},

		uploadImg(event) {
			let file = event.target.files[0];
			if (file.size > 1024 * 1024 * 2) {
				this.alert_text = "上传失败，只能传2M以内的图片";
				this.alert_show = true;
			} else {
				let form_data = new FormData();
				form_data.append('file', file);
				if (!this.loading) {
					this.loading = true;
					this.$http.post('http://120.79.182.246:5000/api/uploadImg', form_data)
						.then( (res) => {
							this.loading = false;
							if ( res.data.code == 0) {
								this.alert_text = res.data.err;
								this.alert_show = true;
							} else {
								this.img_list.push(res.data.data);
							}
						})
				}
			}	
		},

		delImg(item, index) {
			this.$http.get('http://120.79.182.246:5000/api/deleteImg?filename=' + item)
				.then( (res) => {
					this.img_list.splice(index, 1);
				})
		},

		uploadComment () {
			let form_data = new FormData();
			let username;
			let avatar;
			if (this.hide_name) {
				username = '匿名用户';
				avatar = 'http://p0.meituan.net/aichequan/8632d93a19c8883727301f82cc88501d4301.png';
			} else {
				username = this.user.name;
				avatar = this.user.avatar
			}
			form_data.append('id', this.order_id);
			form_data.append('username', username);
			form_data.append('avatar', avatar);
			form_data.append('drate', this.d_rate);
			form_data.append('srate', this.s_rate);
			form_data.append('content', this.comment_content);
			form_data.append('imglist', JSON.stringify(this.img_list));
			this.$http.post('http://120.79.182.246:5000/api/uploadComment', form_data)
				.then( (res) => {
					this.$router.go(-1);
				})
		}
	},
	computed: {
		...mapGetters([
			'user'
		]),

		canSub () {
			if (this.d_rate && this.s_rate && this.comment_content.length >= 15) {
				return true;
			} else {
				return false;
			}
		} 
	},
	created () {
		this.order_id = this.$router.history.current.query.id;
		this.shop_img = this.$router.history.current.query.img;
		this.shop_name = this.$router.history.current.query.name;
	}
} 
</script>

<style scoped>
.comment {
	position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  overflow-y: auto;
  background: #f4f4f4;
}

.header {
	position: relative;
	padding: 0 5px;
	text-align: center;
	background: #fff;
	line-height: 52px;
}

.back {
	position: absolute;
	left: 10px;
}

.back i {
	font-size: 25px;
}

.title {
	flex: 1;
  font-size: 25px;
  font-weight: 700;
  text-align: center;
}

.comment-box {
	margin: 40px 20px 0 20px;
	background: #fff;
}

.shop-info {
	display: flex;
	margin: 0 10px;
	padding: 10px 0;
	align-items: center;
	justify-content: center;
	border-bottom: 1px solid #d6d6d6; /*no*/
}

.shop-img {
	margin-right: 10px;
}

.shop-img img {
	border-radius: 50%;
}

.shop-name {
	font-size: 20px;
	font-weight: 700;
}

.type {
	display: flex;
	margin: 0 10px;
	padding: 10px 0;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	border-bottom: 1px solid #d6d6d6; /*no*/
}

.type-item {
	padding: 10px;
}

.type-name {
	display: block;
	margin-bottom: 10px;
	font-weight: 700;
	font-size: 16px;
	text-align: center;
}

.star {
	margin-bottom: 10px;
}

.star span {
	margin-right: 10px;
}

.active {
	color: #ffd161;
}

.star-tip {
	display: block;
	text-align: center;
	font-size: 14px;
	color: #ffd161;
}

.input-box {
	position: relative;
	margin: 0 10px;
	padding: 20px 0;
}

.input-box textarea {
	box-sizing: border-box;
	height: 140px;
	width: 100%;
	padding: 5px;
	resize: none;
	line-height: 20px;
  font-size: 16px;
}

.fifteen-tip {
	position: absolute;
	right: 6px;
	bottom: 26px;
	font-size: 14px;
	color: #838383;
}

.upload-img {
	display: flex;
	margin: 0 10px;
	padding-bottom: 20px;
	flex-wrap: wrap;
}

.img-box {
	position: relative;
	margin-right: 10px;
	margin-bottom: 20px;
	height: 80px;
	width: 80px;
	border: 1px solid #d6d6d6; /*no*/
}

.img-box img {
	width: 100%;
	height: 100%;
}

.icon-jian {
	position: absolute;
	top: -8px;
	right: -8px;
	color: #fff;
	background: #ff4c45;
	border-radius: 50%;
}

.upload-box {
	display: flex;
	height: 80px;
	width: 80px;
	margin-right: 10px;
	align-items: center;
	justify-content: center;
	border: 1px solid #d6d6d6; /*no*/
}

.icon-xiangji {
	font-size: 26px;
}

.upload-tip {
	display: flex;
	flex-direction: column;
	justify-content: center;
}

.upload-tip h3 {
	font-size: 18px;
	font-weight: 700;
	margin-bottom: 5px;
}

.upload-tip span {
	font-size: 16px;
	color: #838383;
}

.hide-name {
	display: flex;
	margin: 0 10px;
	padding-bottom: 20px;
	align-items: center;
}

.circle {
	display: inline-block;
	width: 28px;
	height: 28px;
	margin-right: 10px;
	border-radius: 50%;
	border: 1px solid #d6d6d6; /*no*/
}

.hide {
	background: #ffd161;
}

.hide-name span {
	font-size: 18px;
}

.cant-sub {
	position: absolute;
  bottom: 0;
  left: 0;
  height: 60px;
  width: 100%;
  line-height: 60px;
  background: #808080;
  color: #fff;
  text-align: center;
  font-size: 20px;
}

.submit {
	position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 60px;
  width: 100%;
  line-height: 60px;
  background: #ffd161;
  text-align: center;
  font-size: 20px;
  z-index: -1;
}
</style>


