<template>
	<div class="category">
		<div class="header">
			<span class="back" @click="goback"><i class="iconfont icon-back"></i></span>
			<span class="title">{{ titleText }}</span>
		</div>
		<div class="shopSelect">
			<span>综合排序 <i class="iconfont icon-unfold"></i></span>
			<span>销量最高</span>
			<span>距离最近</span>
			<span>筛选</span>
		</div>
		<div v-if="shopdata" class="shoplist" ref="wrapper">
			<div class="shops">
				<router-link tag="div" class="eachShop" v-for="item in shopdata" :key="item.id" :to="{path:'/shop', query:{id:item._id}}">
					<div class="shop-img">
						<img :src="item.imgUrl">
					</div>
					<div class="shop-detail">
						<h4>{{ item.title }}</h4>
						<div class="detail-info">
							<div class="star">
								<span v-for="item in parseInt(item.star)" class="one"></span>
								<span v-if="parseFloat(item.star) - parseInt(item.star) >=0.5 ? 1 : 0" class="half"></span>
								<span v-for="item in (5 - parseInt(item.star) - (parseFloat(item.star) - parseInt(item.star) >=0.5 ? 1 : 0) )" class="zero"></span>
							</div>
							<span class="monthSale">月售{{ item.monthSale }}笔</span>
							<div class="delivery-msg">
								<span class="dl-distence">{{ item.distance }}km</span>
								<span class="dl-time">{{ item.dltime }}分钟/</span>
							</div>
						</div>
						<div class="price-info">
							<span>起送 ¥ {{ item.mPrice }} |</span>
							<span>配送 ¥ {{ item.dPrice }} |</span>
							<span>人均 ¥ {{ item.aPrice }}</span>
						</div>
						<div class="activity-info">
							<div class="discount-box">
								<div class="discount-img">
									<img src="http://p0.meituan.net/xianfu/f8bc8dffdbc805878aa3801a33f563cd1001.png">
								</div>
								<span class="discount-info">{{ item.activity[0].text }}</span>
							</div>
							<div class="discount-box">
								<div class="discount-img">
									<img src="http://p1.meituan.net/xianfu/9c997ecce6150671b8459738a26f8bd9767.png">
								</div>
								<span class="discount-info">{{ item.activity[1].text }}</span>
							</div>
						</div>
					</div>
				</router-link>
				<div class="loadbox">
					<span v-show="loading && !noMore">正在努力加载中...</span>
					<span v-show="noMore" >已经到底了</span>
				</div>
			</div>
		</div>
		<alert-msg :alert_text='alert_text' :alert_show='alert_show' @update="update"></alert-msg>
	</div>
</template>

<script>
import AlertMsg from "../components/alertmsg"
import BScroll from 'better-scroll'
import {mapGetters, mapActions} from 'vuex'

export default {
	components: {
		AlertMsg
	},
	data () {
		return {
			page: 1,
			shopdata: [],
			loading: false,
			noMore: false,
			preventRepeat: false,
			shoptype: '',
			titleText: '',
			alert_text: '该分类下暂无商家数据！请返回首页重新筛选！',
			alert_show: false,
		}
	},
	methods: {

		//监听better-scroll滚动事件 判断是否滑动到底部 加载更多
		listenScroll () {
			this.scroll.on('scroll', (position) => {
				//如果到达底部，异步加载更多数据
				if (this.scroll.wrapperHeight - position.y >= this.scroll.scrollerHeight) {
					//避免加载过程中重复请求
					if (!this.loading) {
						this.loading = true;
						//因为异步数据返回很快，设置了一个延时来模拟加载过程
						setTimeout( () => {
							this.getMore();
						}, 1000);
						//加载完成后，刷新scroll
						this.scroll.refresh();
					};
				};
			})
		},


		//初始化BScroll
		_initBScroll () {
			this.scroll = new BScroll(this.$refs.wrapper, {
				click: true,
				probeType: 2,
			});
		},


		//拉取商家
		getShops () {
			this.$http.get('http://120.79.182.246:5000/api/getshops?type=' + this.shoptype + '&page=' + this.page + '&location=' + this.location)
				.then( (res) => {
					if (res.data.data.length > 0) {
						this.shopdata = res.data.data;
						this.$nextTick(() => {
							//dom渲染完成 初始化better-scroll
							this._initBScroll();
							this.listenScroll();
						});
					} else {
						this.alert_show = true;
					}
				});
		},

		//加载更多商家
		getMore () {
			if (!this.noMore && !this.preventRepeat) {
				this.page ++;
				this.preventRepeat = true;
				this.$http.get('http://120.79.182.246:5000/api/getshops?type=' + this.shoptype + '&page=' + this.page + '&location=' + this.location)
					.then( (res) => {
						if (res.data.data.length < 4) {
							this.noMore = true;
						};
						res.data.data.forEach( (item) => {
							this.shopdata.push(item);
						});
					});
				this.preventRepeat = false;
				this.loading = false;
			}
		},

		goback () {
			this.$router.push('/index');
		},

		update (status) {
			this.alert_show = status;
		},

		...mapActions([
			'getLocation'
		])
	},
	computed: {
		...mapGetters([
			'location'
		])
	},
	watch: {
		location (newVal, oldVal) {
			if (oldVal !== '') {
				this.noMore = false;
				this.loading = false;
				this.shopdata = [];
				this.page = 1;
				this.getShops();
			} 
			if (!oldVal) {
				this.getShops();
			}
		}
	},
	created () {
		this.shoptype = this.$route.query.type;
		this.titleText = this.$route.query.name;
		if (!this.location) {
			this.getLocation();
		} else {
			this.getShops();
		}
	}
}
</script>

<style scoped>
.location {
	position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  overflow-y: auto;
  background: #f4f4f4;
}

.header {
	position: relative;
	padding: 0 5px;
	text-align: center;
	background: #fff;
	line-height: 52px;
}

.back {
	position: absolute;
	left: 10px;
}

.back i {
	font-size: 25px;
}

.title {
	flex: 1;
  font-size: 25px;
  font-weight: 700;
  text-align: center;
}

.shopSelect {
	display: flex;
	flex-wrap: wrap; 
	border-bottom: 1px solid #e4e4e4; /*no*/
	padding: 0 10px;
	line-height: 40px;
}

.shopSelect span {
	flex: 1;
	font-size: 16px;
	text-align: center;
}

.shoplist {
	height: 850px;
	overflow: hidden;
}

.eachShop {
	display: flex;
	margin: 0 20px;
	padding-top: 30px;
	border-bottom: 1px solid #d6d6d6; /*no*/
}

.shop-img {
	height: 80px;
	width: 180px;
	margin-right: 10px;
	border: 1px solid #d6d6d6; /*no*/
}

img {
	display: block;
	height: 100%;
	width: 100%;
}

.shop-detail {
	width: 100%;
	margin-bottom: 20px;
}

.shop-detail h4 {
	font-size: 20px;
  font-weight: 700;
}

.detail-info {
	display: flex;
	margin: 20px 0;
	width: 100%;
	align-items: center;
}

.star {
	display: flex;
	align-items: center;
}

.star span {
	flex: 1;
  width: 12px;
  height: 12px;
  margin-right: 3px;
  background-repeat: no-repeat;
  background-size: 100%;
}

.one {
	background-image: url('../assets/star24_on@2x.png');
}

.half {
	background-image: url('../assets/star24_half@2x.png');
}

.zero {
	background-image: url('../assets/star24_off@2x.png');
}

.monthSale {
	flex: 0 0 25%;
	font-size: 12px;
	margin-left: 5px;
}

.delivery-msg {
	flex: 1;
	display: flex;
	font-size: 12px;
	flex-direction: row-reverse;
}

.price-info span {
	font-size: 12px;
}

.activity-info {
	padding-top: 20px;
}

.discount-box {
	display: flex;
	align-items: center;
	margin-bottom: 8px;	
}

.discount-img {
	display: inline-block;
	width: 18px;
  height: 18px;
  margin-right: 10px;
}

.discount-info {
	display: inline-block;
	width: 240px;
	font-size: 13px; 
	color: #777272;
	overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.loadbox {
	margin: 5px 15px;
	text-align: center;
	font-size: 14px;
}

.loadbox span {
	display: block;
}
</style>


