<template>
	<div class="bottomTab">
		<router-link v-for="(item, index) in choice" :key="item.id" tag="div" class="tab-item" :to="item.url">
			<div class="imgbox">
				<img :src="[item.isActive ? activeImg[index].imgsrc:defaultImg[index].imgsrc]">
			</div>
			<span>{{ item.text }}</span>
		</router-link>
	</div>
</template>

<script>
	export default {
		props: [
			'currentIndex'
		],
		data () {
			return {
				choice: [ 
					{isActive: false, text: '首页', url:'/index'},
					{isActive: false, text: '订单', url:'/order'},
					{isActive: false, text: '我的', url:'/home'}
				],
				defaultImg: [
					{imgsrc: require('../assets/index.png')},
					{imgsrc: require('../assets/order.png')},
					{imgsrc: require('../assets/home.png')}
				],
				activeImg: [
					{imgsrc: require('../assets/index-active.png')},
					{imgsrc: require('../assets/order-active.png')},
					{imgsrc: require('../assets/home-active.png')}
				]
			}
		},
		created () {
			this.choice[this.currentIndex].isActive = true;
		}
	}
</script>

<style scoped>
.bottomTab {
	display: flex;
	width: 100%;
	position: fixed;
	bottom: 0;
	border-top: 1px solid #d3d3d3; /*no*/
	background: #fff;
}

.tab-item {
	flex: 1;
	text-align: center;
	font-size: 12px;
}

.imgbox {
	width: 30px;
	height: 30px;
	margin: 2px auto;
}

.imgbox img {
	width: 100%;
	height: 100%;
}
</style>


