import * as types from './mutation-types.js'
import Vue from 'Vue'

//定义mutation修改操作
const mutations = {
	[types.SET_LOCATION](state, location){
		state.location = location
	},

	[types.SET_ADDRESS](state, address){
		state.address = address
	},

	[types.SET_USER](state, user){
		state.user = user
	},

	[types.SET_LOGIN](state, status){
		state.login = status
	},

	[types.UPDATE_USER](state, user){
		state.user = user
	},

	[types.SET_SHOP](state, shop){
		state.shop = shop
	},

	[types.ADD_GOOD](state, {shop_id, shop_name, shop_img, good_name, good_price, good_img}){
		let cart = state.cartlist;
		let shop;
		//如果该商店还没有添加过商品，进行初始化
		if (!cart[shop_id]) {
			shop = cart[shop_id] = {
				totalPrice: 0, //总价格
				totalNum: 0, //总商品数量
				name: shop_name, //商店名
				img: shop_img, //商店图片
			};
		} else {
			shop = cart[shop_id];
		}
		//对相应属性进行赋值
		shop.totalPrice = (Number(shop.totalPrice) + Number(good_price)).toFixed(2);
		shop.totalNum++;
		//如果该商品没有添加过，进行初始化，否则数量+1
		if (!shop[good_name]) {
			shop[good_name] = {
				name: good_name,
				price: good_price,
				img: good_img,
				num: 1,
			}
		} else {
			shop[good_name].num++;
		}
		//进行更新操作
		state.cartlist = cart;
	},

	[types.REMOVE_GOOD](state, {shop_id, good_name}){
		let cart = state.cartlist;
		let shop = cart[shop_id];
		//对相应属性进行修改
		shop.totalPrice = (Number(shop.totalPrice) - Number(shop[good_name].price)).toFixed(2);
		shop.totalNum--;
		if (shop.totalNum === 0) {
			delete cart[shop_id];
		} else if (shop[good_name].num === 1) {
			delete shop[good_name];
		} else {
			shop[good_name].num--;
		}
		//进行更新操作
		state.cartlist = cart;
	},

	[types.EMPTY_CART](state, shop_id){
		let cart = state.cartlist;
		delete cart[shop_id];
		state.cartlist = cart;
	},

	[types.SET_RECEIVE_ADDRESS](state, address){
		state.r_address = address
	},
}

export default mutations


