<template>
	<div class="comments" ref="wrapper">
		<div>
			<div class="score-box">
				<div class="shop-rate">
					<span class="score">{{ shop.star }}</span>
					<span class="score-type">商家评分</span>
				</div>
				<div class="detail-rate">
					<div class="rate-box">
						<span class="type-name">口味</span>
						<div class="rate-star">
							<span v-for="item in parseInt(shop.taste)" class="one"></span>
							<span v-if="parseFloat(shop.taste) - parseInt(shop.taste) >=0.5 ? 1 : 0" class="half"></span>
							<span v-for="item in (5 - parseInt(shop.taste) - (parseFloat(shop.taste) - parseInt(shop.taste) >=0.5 ? 1 : 0) )" class="zero"></span>
						</div>
						<span class="type-score">{{ shop.taste }}</span>
					</div>
					<div class="rate-box">
						<span class="type-name">包装</span>
						<div class="rate-star">
							<span v-for="item in parseInt(shop.package)" class="one"></span>
							<span v-if="parseFloat(shop.package) - parseInt(shop.package) >=0.5 ? 1 : 0" class="half"></span>
							<span v-for="item in (5 - parseInt(shop.package) - (parseFloat(shop.package) - parseInt(shop.package) >=0.5 ? 1 : 0) )" class="zero"></span>
						</div>
						<span class="type-score">{{ shop.package }}</span>
					</div>
				</div>
				<div class="delivery-rate">
					<span class="score">{{ shop.d_rate }}</span>
					<span class="score-type">配送评分</span>
				</div>
			</div>
			<div class="comment-box">
				<div class="select-type">
					<span class="active">全部</span>
					<span>有图</span>
					<span><i class="iconfont icon-dianping"></i>(0.0分)</span>
				</div>
				<div class="comment-item" v-for="item in shop_comments" :key="item.id">
					<div class="avatar">
						<img v-lazy="item.avatar">
					</div>
					<div class="right-box">
						<div class="name-time">
							<span class="username">{{ item.name }}</span>
							<span class="time">{{ item.date.substr(0, 10) }}</span>
						</div>
						<div class="user-score">
							<span class="score-text">评分</span>
							<div class="user-rate">
								<span v-for="item in parseInt(item.d_rate)" class="one"></span>
								<span v-if="parseFloat(item.d_rate) - parseInt(item.d_rate) >=0.5 ? 1 : 0" class="half"></span>
								<span v-for="item in (5 - parseInt(item.d_rate) - (parseFloat(item.d_rate) - parseInt(item.d_rate) >=0.5 ? 1 : 0) )" class="zero"></span>
							</div>
						</div>
						<div class="content">
							{{ item.content }}
						</div>
						<div class="img-box" v-if="item.img.length">
							<div class="each-img" v-for="imgurl in item.img">
								<img v-lazy="imgurl">
							</div>
						</div>
					</div>
				</div>
			</div>
			<div class="loadbox">
				<span v-show="loading && !noMore">正在努力加载中...</span>
				<span v-show="noMore" >已经到底了</span>
			</div>
		</div>
	</div>
</template>

<script>
import BScroll from 'better-scroll'
import {mapGetters} from 'vuex'

export default {
	data () {
		return {
			shop_comments: [],
			page: 1,
			loading: false,
			noMore: false,
			preventRepeat: false,
		}
	},
	computed: {
		...mapGetters([
			'shop'
		])
	},
	methods: {
		//监听better-scroll滚动事件 判断是否滑动到底部 加载更多
		listenScroll () {
			this.scroll.on('scroll', (position) => {
				//如果到达底部，异步加载更多数据
				if (this.scroll.wrapperHeight - position.y > this.scroll.scrollerHeight) {
					//避免加载过程中重复请求
					if (!this.loading) {
						this.loading = true;
						//因为异步数据返回很快，设置了一个延时来模拟加载过程
						setTimeout( () => {
							this.getMore();
						}, 1000);
						//加载完成后，刷新scroll
						this.scroll.refresh();
					};
				};
			})
		},


		//初始化BScroll
		_initBScroll () {
			this.scroll = new BScroll(this.$refs.wrapper, {
				probeType: 2,
			});
		},


		//拉取评论
		getComments () {
			this.$http.get('http://120.79.182.246:5000/api/getcomments?id=' + this.shop._id + '&page=' + this.page)
				.then( (res) => {
					this.shop_comments = res.data.data;
					this.$nextTick(() => {
						//dom渲染完成 初始化better-scroll
						this._initBScroll();
						this.listenScroll();
					});
				});
		},

		//加载更多评论
		getMore () {
			if (!this.noMore && !this.preventRepeat) {
				this.page ++;
				this.preventRepeat = true;
				this.$http.get('http://120.79.182.246:5000/api/getcomments?id=' + this.shop._id + '&page=' + this.page)
					.then( (res) => {
						if (res.data.data.length < 10) {
							this.noMore = true;
						};
						res.data.data.forEach( (item) => {
							this.shop_comments.push(item);
						});
					});
				this.preventRepeat = false;
				this.loading = false;
			}
		},
	},
	created () {
		this.getComments();
	}
}

</script>

<style scoped>
.comments {
	position: absolute;
	top: 283px;
	bottom: 0;
	width: 100%;
	overflow: hidden;
	flex-direction: column;
}

.score-box {
	display: flex;
	height: 120px;
	margin: 20px 0;
	padding: 0 20px;
	align-items: center;
	background: #fff;
}

.shop-rate, .delivery-rate {
	flex: 0 0 100px;
	display: flex;
	flex-direction: column;
	align-items: center;
	font-size: 16px;
}

.shop-rate .score {
	font-size: 50px;
	margin-bottom: 10px;
	color: #ffd161;
}

.delivery-rate .score {
	font-size: 50px;
	margin-bottom: 10px;
	color: #838383;
}

.detail-rate {
	flex: 1;
	display: flex;
	flex-direction: column;
	align-items: center;
}

.rate-box {
	display: flex;
	padding: 15px 0;
	align-items: center;
}

.type-name {
	font-size: 20px;
}

.rate-star {
	flex:1 ;
	display: flex;
	margin: 0 10px;
}

.rate-star span {
	flex: 1;
  width: 16px;
  height: 16px;
  margin-right: 5px;
  background-repeat: no-repeat;
  background-size: 100%;
}

.one {
	background-image: url('../assets/star24_on@2x.png');
}

.half {
	background-image: url('../assets/star24_half@2x.png');
}

.zero {
	background-image: url('../assets/star24_off@2x.png');
}

.type-score {
	font-size: 24px;
	color: #ffd161;
}

.comment-box {
	display: flex;
	background: #fff;
	flex-direction: column;
}

.select-type {
	flex: 0 0 50px;
	display: flex;
	padding: 20px 20px 0 20px;
	align-items: center;
	justify-content: center;
}

.select-type span {
	flex: 1;
	line-height: 50px;
	font-size: 20px;
	text-align: center;
	color: #ffd161;
	border: 1px solid #ffb000; /*no*/
	border-radius: 2px;
}

.select-type span i {
	font-size: 19px;
	margin: 0 5px 0 0;
}

.select-type .active {
	background: #ffd161;
	color: #000;
}

.comment-item {
	display: flex;
	padding: 20px 10px;
}

.avatar {
	flex: 0 0 60px;
	height: 60px;
	margin-right: 10px;
}

.avatar img {
	width: 100%;
	height: 100%;
	border-radius: 50%;
}

.right-box {
	flex: 1;
	display: flex;
	flex-direction: column;
	border-bottom: 1px solid #d6d6d6; /*no*/
}

.name-time {
	display: flex;
	justify-content: space-between;
	margin-bottom: 10px;
}

.username, .time {
	font-size: 14px;
	font-weight: 700;
}

.user-score {
	display: flex;
	margin-bottom: 10px;
}

.score-text {
	margin-right: 5px;
	font-size: 14px;
	font-weight: 700;
	color: #838383;
}

.user-rate {
	display: flex;
}

.user-rate span {
	flex: 1;
  width: 14px;
  height: 14px;
  margin-right: 5px;
  background-repeat: no-repeat;
  background-size: 100%;
}

.content {
	font-size: 14px;
	padding-bottom: 10px;
}

.img-box {
	display: flex;
	flex-wrap: wrap;
	margin-bottom: 10px;
}

.each-img {
	display: inline-block;
	width: 60px;
	height: 60px;
	margin-right: 10px;
	border: 1px solid #d6d6d6; /*no*/
}

.each-img img {
	width: 100%;
	height: 100%;
}

.loadbox {
	display: flex;
	align-items: center;
	justify-content: center;
	height: 10px;
	background: #fff;
	font-size: 14px;
}
</style>


