<template>
	<div class="pay-result">
		<div class="header">
			<span class="back" @click="goback"><i class="iconfont icon-back"></i></span>
			<span class="title">订单详情</span>
		</div>
		<div class="result-box">
			<span class="result">订单已完成</span>
			<span class="tip">感谢您对华阳外卖的支持，期待再次光临。</span>
			<span class="one-more" @click="oneMore">再来一单</span>
		</div>
		<div class="order-detail">
			<div class="shop-info">
				<div class="shop-img">
					<img :src="shop_img">
				</div>
				<span class="shop-name">{{ shop_name }}</span>
			</div>
			<ul>
				<li class="good-item" v-for="item in order_info.goods" :key="item.id">
					<div class="good-img">
						<img width="105" height="79" :src="item.img">
					</div>
					<div class="right-box">
						<div class="name-price">
							<span class="good-name">{{ item.name }}</span>
							<span class="good-price">¥{{ item.price }}</span>
						</div>
						<span class="count">X{{ item.num }}</span>
					</div>
				</li>
			</ul>
			<div class="orther">
				<div class="fee">
					<span class="fee-name">包装费</span>
					<span class="fee-price">¥0</span>
				</div>
				<div class="fee">
					<span class="fee-name">配送费</span>
					<span class="fee-price">¥0</span>
				</div>
			</div>
			<div class="price">
				<span class="discount">已优惠¥0</span>
				<span class="total">合计¥{{ order_info.total_price }}</span>
			</div>
			<span class="contact-seller"><i class="iconfont icon-phone"></i> 电话商家</span>
		</div>
		<div class="delivery-info" v-if="order_info.r_address">
			<div>
				<span class="d-title">期望时间</span>
				<span class="d-detail">立即配送</span>
			</div>
			<div>
				<span class="d-title">配送信息</span>
				<span class="d-detail">
					<span class="contact-info">联系人:<i v-if="order_info.r_address.sex === 'male'" class="iconfont icon-male"></i><i v-else class="iconfont icon-female"></i>{{ order_info.r_address.name }}</span>
					<span class="contact-info">联系电话:<span class="phone">{{ order_info.r_address.number }}</span></span>
				</span>
			</div>
			<div>
				<span class="d-title">配送地址</span>
				<span class="d-detail"><i class="iconfont icon-location"></i>{{ order_info.r_address.address }}</span>
			</div>
			<div>
				<span class="d-title">配送服务</span>
				<span class="d-detail">由 商家 提供高品质送餐服务</span>
			</div>
		</div>
		<div class="order-about">
			<div>
				<span class="a-title">订单时间</span>
				<span class="a-detail">{{ order_info.date }}</span>
			</div>
			<div>
				<span class="a-title">支付方式</span>
				<span class="a-detail">{{ order_info.pay_type }} {{ order_info.pay_way }}</span>
			</div>
		</div>
	</div>
</template>

<script>
import {mapGetters, mapMutations} from 'vuex'

export default {
	data () {
		return {
			order_id: '',
			order_info: {},
			from_Order: false,
			shop_img: '',
			shop_name: '',
		}
	},
	methods: {
		goback () {
			if (!this.from_Order) {
				this.$router.push('/index');
			} else {
				this.$router.go(-1);
			}
		},

		oneMore () {
			this.$router.push({path:'/shop', query:{id:this.order_info.shop_id}});
		},

		...mapMutations ({
			emptyCart: 'EMPTY_CART'
		})
	},
	computed: {
		...mapGetters([
			'shop',
		])
	},
	created () {
		if (this.$route.query.fromOrder) {
			this.from_Order = true;
		}
		if (!this.from_Order) {
			this.emptyCart(this.shop._id);
		}
		this.order_id = this.$router.history.current.query.id;
		this.$http.get('http://120.79.182.246:5000/api/getOrderInfo?id=' + this.order_id)
			.then( (res) => {
				this.order_info = res.data.data;
				this.$http.get('http://120.79.182.246:5000/api/getshop?id=' + this.order_info.shop_id)
					.then( (res) => {
						this.shop_img = res.data.data.imgUrl;
						this.shop_name = res.data.data.title;
					});
			});
	}
}
</script>

<style scoped>
.pay-result {
	position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  overflow-y: auto;
  background: #f4f4f4;
}

.header {
	position: relative;
	padding: 0 5px;
	text-align: center;
	background: #fff;
	line-height: 52px;
}

.back {
	position: absolute;
	left: 10px;
}

.back i {
	font-size: 25px;
}

.title {
	flex: 1;
  font-size: 25px;
  font-weight: 700;
  text-align: center;
}

.result-box {
	display: flex;
	margin: 20px;
	padding: 20px 0;
	height: 140px;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	background: #fff;
}

.result-box span {
	display: block;
}

.result {
	margin-bottom: 20px;
	font-size: 18px;
	font-weight: 700;
}

.tip {
	margin-bottom: 20px;
	font-size: 16px;
	color: #838383;
}

.one-more {
	width: 120px;
	height: 40px;
	line-height: 40px;
	font-size: 16px;
	text-align: center;
	background: #ffd161;
}

.order-detail {
	margin: 0 20px 20px 20px;
	background: #fff;
}

.shop-info {
	display: flex;
	align-items: center;
	padding: 10px;
}

.shop-img {
	display: inline-block;
	width: 35px;
	height: 35px;
	margin-right: 5px;
}

.shop-img img {
	width: 100%;
	height: 100%;
	border-radius: 50%;
}

.shop-name {
	font-size: 16px;
	color: #838383;
}

.good-item {
	display: flex;
	margin-bottom: 5px;
	padding: 10px;
	background: #E1E2E3;
}

.good-img {
	margin-right: 15px;
}

.right-box {
	flex: 1;
	display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.name-price {
	display: flex;
	justify-content: space-between;
}

.name-price span {
	font-size: 16px;
}

.count {
	font-size: 14px;
	color: #838383;
}

.orther {
	margin: 10px 0;
	padding: 10px 10px 0 10px;;
}

.fee {
	display:flex;
	justify-content: space-between;
	padding-bottom: 20px;
}

.fee-name, .fee-price {
	font-size: 16px;
}

.price {
	margin: 0 10px;
	padding: 20px 10px;
	text-align: right;
	border-top: 1px dashed #999; /*no*/
}

.discount, .total {
	font-size: 20px;
	font-weight: 500;
}

.discount {
	color: #999;
}

.total {
	color: #fb4e44;
	font-weight: 700;
}

.contact-seller {
	display: flex;
	justify-content: center;
	padding: 20px 10px;
	margin: 0 10px;
	border-top: 1px dashed #999; /*no*/
	font-size: 20px;
}

.contact-seller i {
	font-size: 20px;
	margin-right: 10px;
}

.delivery-info {
	margin: 0 20px 20px 20px;
	padding: 0 10px;
	background: #fff;
}

.delivery-info div {
	display: flex;
	padding: 10px 0;
	height: 20px;
	line-height: 20px;
	font-size: 14px;
}

.d-title {
	flex: 0 0 80px;
	color: #838383;
	margin-right: 20px;
}

.d-detail i {
	color: #ffd161;
}

.contact-info {
	margin-right: 20px;
}

.phone {
	color: #368ced;
}

.order-about {
	margin: 0 20px 20px 20px;
	padding: 0 10px;
	background: #fff;
}

.order-about div {
	display: flex;
	padding: 10px 0;
	height: 20px;
	line-height: 20px;
	font-size: 14px;
}

.a-title {
	flex: 0 0 80px;
	color: #838383;
	margin-right: 20px;
}
</style>



