import * as types from './mutation-types.js'
import axios from 'axios'

export const getLocation = function ({commit, state}) {
	axios.get('http://120.79.182.246:5000/api/getlocation?ip=' + returnCitySN.cip)
		.then( (res) => {
			let location =  res.data.data.location;
			let address = res.data.data.address;
			commit(types.SET_LOCATION, location);
			commit(types.SET_ADDRESS, address);
		})
}

export const loginUser = function ({commit, state}, user) {
	commit(types.SET_USER, user);
	commit(types.SET_LOGIN, true);
}

export const logoutUser = function ({commit, state}) {
	commit(types.SET_USER, {});
	commit(types.SET_LOGIN, false);
}


