<template>
	<div class="order">
		<div class="header">
			<span class="back" @click="goback"><i class="iconfont icon-back"></i></span>
			<span class="title">订单</span>
		</div>
		<div v-if="!hasLogin" class="loginTip">
			<div>
				<i class="iconfont icon-my"></i>
			</div>
			<span class="loginMsg">您还没有登录，请登录后查看订单信息</span>
			<router-link to="/login"><span class="login">登录/注册</span></router-link>
		</div>
		<div v-else class="orderbox">
			<span v-if="!this.order_list.length" class="orderMsg">订单空空如也，快去购物吧！</span>
			<div v-else class="order-item" v-for="item in order_list">
				<div class="shop-img" @click="TurnToDetail(item)">
					<img width="105" height="79" :src="item.shop_img">
				</div>
				<div class="right-box">
					<div class="order-info" @click="TurnToDetail(item)">
						<span class="shop-name">{{ item.shop_name }}</span>
						<span class="date">下单时间: {{ item.date }}</span>
						<span class="price">总价: ¥{{ item.total_price }}</span>
					</div>
					<div class="other">
						<span v-if="!item.hasComment" class="comment" @click="TurnToComment(item)">去评价</span>
						<span v-else class="comment">已评价</span>
						<span class="one-more" @click="oneMore(item)">再来一单</span>
					</div>
				</div>
			</div>
		</div>
		<b-tab :currentIndex=1></b-tab>
	</div>
</template>

<script>
import BTab from '../components/bottomtab'
import {mapGetters} from 'vuex'

export default {
	components: {
		BTab,
	},
	data () {
		return {
			order_list: [],
		}
	},
	methods: {
		goback () {
			this.$router.go(-1);
		},

		TurnToDetail (item) {
			this.$router.push({path:'/finishPay', query:{id:item.id, fromOrder:true}});
		},

		TurnToComment (item) {
			this.$router.push({path:'/comment', query:{id:item.id, img:item.shop_img, name:item.shop_name}});
		},

		oneMore (item) {
			this.$router.push({path:'/shop', query:{id:item.shop_id}})
		}
	},
	computed: {
		...mapGetters([
			'login', 'user'
		]),

		hasLogin () {
			return this.login ? true : false;
		}
	},
	created () {
		if (this.login) {
			this.$http.get('http://120.79.182.246:5000/api/getUserOrders?username=' + this.user.name)
				.then( (res) => {
					this.order_list = res.data.data;
				})
		}
	}
}
</script>

<style scoped>
.order {
	position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  overflow-y: auto;
  background: #f4f4f4;
}

.header {
	position: relative;
	padding: 0 5px;
	text-align: center;
	background: #fff;
	line-height: 52px;
}

.back {
	position: absolute;
	left: 10px;
}

.back i {
	font-size: 25px;
}

.title {
	flex: 1;
  font-size: 25px;
  font-weight: 700;
  text-align: center;
}

.loginTip {
	width: 100%;
	position: absolute;
	top: 50%;
	transform: translateY(-50%);
	text-align: center;
}

.orderbox {
	margin-top: 20px;
	padding: 0 15px;
	background: #fff;
}

.icon-my {
	font-size: 50px;
	border-radius: 50%;
	background: #D6D6D6;
	color: #fff;
	padding: 10px;
}

.loginMsg {
	display: block;
	margin: 24px 0 16px;
	font-size: 20px;
	color: #9b9997;
}

.orderMsg {
	width: 100%;
	position: fixed;
	top: 50%;
	transform: translateY(-50%);
	display: block;
	font-size: 20px;
	text-align: center;
}

.login {
	display: inline-block;
	width: 45%;
	line-height: 38px;
	font-size: 20px;
	background: #ffd161;
	border-radius: 5px;
	color: #000;
}

.order-item {
	display: flex;
	padding: 20px 0;
	border-bottom: 1px solid #d6d6d6; /*no*/
}

.shop-img {
	margin-right: 20px;
}

.right-box {
	flex: 1;
	display: flex;
	flex-direction: column;
}

.order-info {
	display: flex;
	flex-direction: column;
}

.shop-name {
	font-size: 18px;
	font-weight: 700;
	margin-bottom: 10px;
}

.date, .price {
	font-size: 14px;
	color: #838383;
	margin-bottom: 10px;
}

.other {
	flex: 1;
	display: flex;
	flex-direction: row-reverse;
}

.other span {
	width: 80px;
	height: 30px;
	line-height: 30px;
	font-size: 18px;
	text-align: center;
}

.one-more {
	margin-right: 20px;
	color: #ffd161;
	border: 1px solid #ffd161; /*no*/
	border-radius: 5px;
}

.comment {
	border: 1px solid #fff; /*no*/
	border-radius: 5px;
	background: #ffd161;
}
</style>


