<template>
	<div class="location">
		<div class="header">
			<span class="back" @click="goback"><i class="iconfont icon-back"></i></span>
			<span class="title">选择地址</span>
		</div>
		<div class="searchBox">
			<div class="searchInput">
				<i class="iconfont icon-search"></i>
				<input v-model="keywords" type="text" placeholder="请输入地址">
			</div>
		</div>
		<div v-if="s_results" class="searchResult">
			<ul>
				<li v-for="item in s_results" :key="item.id" @click="selectAddress(item)">
					<h3>{{ item.title }}</h3>
					<span>{{ item.address }}</span>
				</li>
			</ul>
		</div>
	</div>
</template>

<script>
import {mapMutations} from 'vuex'

export default {
	data () {
		return {
			keywords: '',
			s_results: [],
			comefromAdd: false,
			
		}
	},
	watch: {
		keywords (data) {
			if (data !== "") {
				this.$http.get('http://120.79.182.246:5000/api/getsug?kw=' + data)
				.then( (res) => {
					this.s_results = res.data.data.data;
				});
			} else {
				this.s_results = [];
			}
		}
	},
	methods: {
		selectAddress (item) {
			if (!this.comefromAdd) {
				let address = item.address;
				let location = item.location.lat + ',' + item.location.lng;
				this.setLocation(location);
				this.setAddress(address);
				this.$router.push('/index');
			} else {
				this.setRAddress(item.title);
				this.$router.go(-1);
			}
		},
		goback () {
			this.$router.go(-1);
		},
		...mapMutations({
			setLocation: 'SET_LOCATION',
			setAddress: 'SET_ADDRESS',
			setRAddress: 'SET_RECEIVE_ADDRESS',
		})
	},
	created () {
		if (this.$route.query.fromAdd) {
			this.comefromAdd = true;
		}
	}
}
</script>

<style scoped>
.location {
	position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  overflow-y: auto;
  background: #f4f4f4;
}

.header {
	position: relative;
	padding: 0 5px;
	text-align: center;
	background: #fff;
	line-height: 52px;
}

.back {
	position: absolute;
	left: 10px;
}

.back i {
	font-size: 25px;
}

.title {
	flex: 1;
  font-size: 25px;
  font-weight: 700;
  text-align: center;
}

.searchBox {
	display: flex;
  padding-bottom: 10px;
  background: #fff;
  line-height: 40px;
}

.searchInput {
	flex: 1;
	margin: 0 10px;
  padding-left: 10px;
  background: #efeef4;
  border-radius: 20px;
  line-height: 40px;
}

.searchInput input {
  width: 85%;
  border: none;
  text-indent: 10px;
  outline: none;
  background: #efeef8;
  line-height: normal;
  padding: 0;
  margin: 0;
  font-size: 18px;
}

.searchResult ul li {
	border-bottom: 1px solid #e7e7e7; /*no*/
  height: 86px;
  background: #fff;
  font-size: 20px;
  padding-left: 20px;
}

.searchResult ul li h3 {
	margin-bottom: 10px;
  padding-top: 10px;
}

.searchResult ul li span {
	color: #a3a3a3;
}
</style>


