<template>
	<div class="goods">
		<div class="type-wrapper" ref="twrapper">
			<ul>
				<li v-for="(item, index) in goodsdata" :key="item.id" class="typeItem" :class="{'active':currentIndex === index}" @click="selectType(index)">
					<span class="tname">{{ item.tname }}</span>
				</li>
			</ul>
		</div>
		<div class="foods-wrapper" ref="fwrapper">
			<ul>
				<li v-for="item in goodsdata" :key="item.id" class="foodList">
					<h3 class="title">{{ item.tname }}</h3>
					<ul>
						<li v-for="food in item.foods" :key="food.id" class="foodItem">
							<div class="foodImg">
								<img width="105" height="79" v-lazy="food.imgUrl">
							</div>
							<div class="foodInfo">
								<h4 class="fname">{{ food.name }}</h4>
								<span class="monthSale">月售{{ food.monthSale }}份</span>
								<span class="price">¥{{ food.price }}</span>
								<div class="cart-control">
									<cart-control :food="food" @update="calcNumAndTotal" @drop="drop"></cart-control>
								</div>
							</div>
						</li>
					</ul>
				</li>
			</ul>
		</div>
		<div class="ball-container" v-for="ball in balls" :key="ball.id">
       <transition 
          v-on:before-enter="beforeEnter"
          v-on:enter="enter"
          v-on:after-enter="afterEnter"
          name="drop">
         <div class="ball" v-show="ball.show">
             <div class="inner inner-hook">
             </div>
         </div>
       </transition>
    </div>
		<div class="shop-cart">
			<div class="cartContent">
				<div class="left-part" @click="toggleList">
					<span class="cartIcon" :class="{iconActive:bottomShow}">
						<i class="iconfont icon-cart"></i>
						<span class="count-num" v-show="bottomShow">{{ bottomNum }}</span>
					</span>
					<div class="priceTag">
						<span class="totalPrice" v-show="bottomShow">¥{{ bottomTotal }}</span>
						<span class="deliveryPrice">免配送费</span>
					</div>
				</div>
				<span class="right-part" v-if="!bottomShow">¥0起送</span>
				<span v-else class="right-part btnActive" @click="TurnToConfirm">去结算</span>
			</div>
		</div>
		<transition name="fade">
			<div class="cart-list" v-show="bottomShow && ListShow">
				<div class="list-header">
					<h4 class="list-title">购物车</h4>
					<span class="empty-list" @click="emptyNow">清空购物车</span>
				</div>
				<div class="list-content" ref="bwrapper">
					<ul>
						<li v-for="good in currentList" :key="good.id" class="good">
							<span class="good-name">{{ good.name }}</span>
							<span class="good-price">¥{{ good.price * good.count }}</span>
							<div class="good-control">
								<cart-control :food="good" @update="calcNumAndTotal"></cart-control>
							</div>
						</li>
					</ul>
				</div>
			</div>
		</transition>
		<transition name="cover">
			<div class="cover" @click="toggleList" v-show="bottomShow && ListShow"></div>
		</transition>
		<alert-msg :alert_text="alert_text" :alert_show="alert_show" @update="update"></alert-msg>
	</div>
</template>

<script>
import AlertMsg from '../components/alertmsg'
import CartControl from '../components/cartcontrol'
import {mapGetters, mapMutations} from 'vuex'
import BScroll from 'better-scroll'
import Vue from 'vue'

export default {
	components: {
		CartControl,
		AlertMsg
	},
	data () {
		return {
			s_id: '',
			goodsdata: [],
			heightList: [],  //右侧元素高度列表
			scrollR: 0, //右侧实时滚动位置
			bottomShow: false, 
			bottomTotal: 0,
			bottomNum: 0,
			currentList: [],
			ListShow: false,
			balls: [
				{show: false},
				{show: false},
				{show: false},
				{show: false},
				{show: false},
			],
			dropballs: [],
			alert_text: '',
			alert_show: false,
		}
	},
	methods: {
		//初始化BScroll
		_initBScroll () {
			this.typeScroll = new BScroll(this.$refs.twrapper, {
				click: true
			});

			this.foodsScroll = new BScroll(this.$refs.fwrapper, {
				click: true,
				probeType: 3
			});

			this.calcHeight();
			this.listenRight();
		},

		//实时监听右侧滚动位置
		listenRight () {
			this.foodsScroll.on('scroll', (position) => {
				this.scrollR = Math.abs(Math.round(position.y));
			})
		},

		//计算右侧元素高度列表
		calcHeight () {
			let foodlist = this.$refs.fwrapper.getElementsByClassName('foodList');
			let height = 0;
			this.heightList.push(height);
			for (let i = 0; i < foodlist.length; i++){
				let item = foodlist[i];
				height += item.clientHeight;
				this.heightList.push(height);
			}
		},

		//左侧点击事件监听
		selectType (index) {
			let foodlist = this.$refs.fwrapper.getElementsByClassName('foodList');
			let el = foodlist[index]; //对应右侧列表
			this.foodsScroll.scrollToElement(el, 300);
		},

		//计算底部数量标签和总价
		calcNumAndTotal () {
			let result = this.cartlist[this.s_id];
			if (result) {
				this.bottomShow = true;
				this.bottomTotal = result.totalPrice;
				this.bottomNum = result.totalNum;
				this.updateList();
			} else {
				this.bottomShow = false;
				this.bottomTotal = 0;
				this.bottomNum = 0;
				this.currentList = [];
				this.ListShow = false;
			}
		},

		//实时更新当前商店的购物车列表
		updateList () {
			this.currentList = [];
			let list = this.goodsdata;
			list.forEach( (tItem) => {
				let foods = tItem.foods;
				foods.forEach( (food) => {
					if (food.count) {
						this.currentList.push(food);
					}
				})
			})
		},

		//底部购物车列表折叠隐藏控制
		toggleList () {
			if (this.bottomShow) {
				this.ListShow = !this.ListShow;
			} else {
				this.ListShow = false;
			}
		},

		//清空购物车
		emptyNow () {
			let list = this.goodsdata;
			list.forEach( (tItem) => {
				let foods = tItem.foods;
				foods.forEach( (food) => {
					if (food.count) {
						Vue.delete(food, 'count');
					}
				})
			})
			this.emptyCart(this.s_id);
			this.calcNumAndTotal();
		},

		//点击底部结算跳转
		TurnToConfirm () {
			if (!this.login) {
				this.alert_text = "请登录后进行下单操作！";
				this.alert_show = true;
				setTimeout( ()=> {
					this.$router.push({path:'/confirmOrder', query:{id:this.s_id}});
				}, 2000)
			} else {
				this.$router.push({path:'/confirmOrder', query:{id:this.s_id}});
			}
		},

		update (data) {
			this.alert_show = data;
		},

		//小球掉落
		drop (el) {
       for (let i = 0; i < this.balls.length; i++) {
         let ball = this.balls[i];
         if (!ball.show) {
           ball.show = true;
           ball.el = el;
           this.dropballs.push(ball);
           return;
         }
       } 
    },

    //动画开始前
    beforeEnter (el) {
      let count = this.balls.length;
      while (count--) {
        let ball = this.balls[count];
        if (ball.show) {
          /*获取加号的位置(小球动画开始的位置)*/
          let rect = ball.el.getBoundingClientRect();           
          let x = rect.left - 44;
          let y = -(window.innerHeight - rect.top - 49);
          /*注释部分为动画为一层时代码*/
           /*el.style.display = '';*/
           /*el.style.webkitTransform=`translate3d(${x}px,${y}px,0)`;*/
           /*el.style.transform = `translate3d(${x}px, ${y}px, 0)`;*/

          el.style.display = '';
          el.style.webkitTransform = `translate3d(0, ${y}px, 0)`;
          el.style.transform = `translate3d(0, ${y}px, 0)`;
          /*内层动画(这个动画在这里被分成两层，也可以一层)*/
          let inner = el.getElementsByClassName('inner-hook')[0];
          inner.style.webkitTransform = `translate3d(${x}px, 0, 0)`;
          inner.style.transform = `translate3d(${x}px, 0, 0)`;
        }
      }
    },

    //小球进入
    enter (el, done) {
      /* eslint-disable no-unused-vars */ 
      let rf = el.offsetHeight;                 /*触发浏览器重绘;*/
      this.$nextTick(() => {
        el.style.webkitTransform = 'translate3d(0, 0, 0)';
        el.style.transform = 'translate3d(0, 0, 0)';
        let inner = el.getElementsByClassName('inner-hook')[0];
        inner.style.webkitTransform = 'translate3d(0, 0, 0)';
        inner.style.transform = 'translate3d(0, 0, 0)';
        el.addEventListener('transitionend', done);
      });
    },

    //动画完成
    afterEnter (el) {
       let ball = this.dropballs.shift();
       if (ball) {
           ball.show = false;
           el.style.display = 'none';
       }
    },

    ...mapMutations({
    	emptyCart : 'EMPTY_CART'
    })
	},
	computed: {
		...mapGetters([
			'cartlist', 'login'
		]),

		//计算右侧滑动实时位置对应左侧的index
		currentIndex () {
			for (let i = 0; i < this.heightList.length; i++){
				let height1 = this.heightList[i];
				let height2 = this.heightList[i+1];
				if (!height2 || (this.scrollR >= height1 && this.scrollR < height2)) {
					return i;
				}
			}
		}
	},
	watch: {
		ListShow (data) {
			if (data) {
				if (!this.scroll) {
					this.$nextTick(() => {
						this.scroll = new BScroll(this.$refs.bwrapper, {
							click: true
						});
					});
				} else {
					this.scroll.refresh();
				}
			}
		}
	},
	created () {
		this.s_id = this.$router.history.current.query.id;
		this.$http.get('http://120.79.182.246:5000/api/getgoods?id=' + this.s_id)
			.then( (res) => {
				let loadData = res.data.data.foodtype;
				let haveBuy = [];
				let result = this.cartlist[this.s_id];
				if (result) {
					let item;
					for (item in result) {
						if (result[item] instanceof Object) {
							haveBuy.push(result[item]);
						}
					}
				}
				if (haveBuy) {
					let index;
					for (index in haveBuy) {
						loadData.forEach( (tItem) => {
							let foods = tItem.foods;
							foods.forEach( (food) => {
								if (food.name === haveBuy[index].name) {
									Vue.set(food, 'count', haveBuy[index].num);
								}
							})
						})
					}
				}
				this.goodsdata = loadData;
				this.$nextTick( () => {
					this._initBScroll();
					this.calcNumAndTotal();
				});
			})
	}
}

</script>

<style scoped>
.goods {
	display: flex;
	position: absolute;
	top: 283px;
	bottom: 60px;
	width: 100%;
	overflow: hidden;
}

.type-wrapper {
	flex: 0 0 110px;
	width: 110px;
	background: #f3f5f7;
}

.foods-wrapper {
	flex: 1;
	background: #fff;
}

.typeItem {
	display: table;
	height: 90px;
	width: 88px;
	padding: 0 11px;
	line-height: 24px;
}

.active {
	background: #fff;
}

.tname {
	display: table-cell;
	width: 88px;
	vertical-align: middle;
	text-align: center;
	font-size: 20px;
	border-bottom: 1px dashed #d3d3d3; /*no*/
}

.title {
	padding-left: 20px;
	height: 24px;
	line-height: 24px;
	font-size: 20px; 
	font-weight: 700;
	border-left: 3px solid #ffd161; /*no*/
}

.foodItem {
	display: flex;
	position: relative;
	margin: 20px;
	padding-bottom: 20px;
	border-bottom: 1px solid #d6d6d6; /*no*/
}

.foodImg {
	display: 0 0 105px;
	margin-right: 20px;
}

.foodInfo {
	flex: 1;
}

.fname {
	font-size: 16px; 
	font-weight: 700;
}

.monthSale {
	display: block;
	margin: 10px 0;
	font-size: 14px;
}

.price {
	color: #fb4f45;
	font-size: 16px;
	font-weight: 700;
}

.cart-control {
	position: absolute;
	bottom: 20px;
	right: 5px;
}

.shop-cart {
	position: fixed;
	left: 0;
	bottom: 0;
	z-index: 50;
	width: 100%;
	height: 60px;
}

.cartContent {
	display: flex;
	background: #333;
}

.left-part {
	flex: 1;
	background: #333;
}

.right-part {
	display: block;
	flex: 0 0 140px;
	width: 140px;
	line-height: 60px;
	text-align: center;
	font-size: 20px;
	background: #000;
	color: #999;
}

.btnActive {
	background: #ffd161;
	color: #000;
}

.cartIcon {
	display: inline-block;
	position: absolute;
	top: -15px;
	left: 20px;
	width: 68px;
	height: 68px;
	border-radius: 50%;
	background: #666;
	text-align: center;
}

.cartIcon i {
	line-height: 68px;
	font-size: 40px;
	color: #a5a5a5;
}

.iconActive {
	background: #ffd161;
}

.iconActive i {
	color: #000;
}

.count-num {
	position: absolute;
  top: 0;
  right: 0;
  width: 24px;
  line-height: 24px;
  border-radius: 50%;
	background: #fb4e44;
	text-align: center;
	color: #fff;
}

.priceTag {
	display: inline-flex;
	padding-left: 108px;
	width: 180px;
	height: 60px;
	flex-direction: column;
	justify-content: center;
}

.totalPrice {
	display: inline-block;
	font-size: 30px;
	color: #fff;
}

.deliveryPrice {
	font-size: 16px;
	color: #999;
}

.ball {
	position: fixed;
	left: 44px;
	bottom: 49px;
	z-index: 200;
	transition: all 0.4s cubic-bezier(0.49,-0.29,0.75,0.41);
}

.inner {
	width: 16px;
	height: 16px;
	border-radius: 50%;
	background-color: #ffd161;
	transition: all 0.4s linear;
}

.cart-list {
	position: absolute;
	left: 0;
	bottom: 0;
	z-index: 40;
	width: 100%;
}

.list-header {
	height: 40px;
	line-height: 40px;
	padding: 0 18px;
	background: #f3f5f7;
	border-bottom: 1px solid #d6d6d6; /*no*/
}

.list-title {
	float: left;
	font-size: 20px;
	font-weight: 700;
}

.empty-list {
	float: right;
	font-size: 18px;
	font-weight: 700;
	color: #ffd161;
}

.list-content {
	padding: 0 18px;
	max-height: 280px;
	overflow: hidden;
	background: #fff;
}

.good {
	position: relative;
	padding: 12px 0;
	box-sizing: border-box;
	border-bottom: 1px solid #d6d6d6; /*no*/
}

.good-name {
	line-height: 40px;
	font-size: 24px;
}

.good-price {
	position: absolute;
	right: 130px;
	bottom: 12px;
	line-height: 40px;
	font-weight: 700;
	font-size: 16px;
	color: #fb4f45;
}

.good-control {
	position: absolute;
	right: 0;
	bottom: 14px;
}

.cover {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 1;
	background: rgba(9, 9, 9, 0.5);
}

.fade-enter-active, .fade-leave-active {
	transition: opacity .5s;
}
.fade-enter, .fade-leave{
	opacity: 0;
}

.cover-enter-active, .cover-leave-active {
	transition: opacity .5s;
}

.cover-enter, .cover-leave {
	opacity: 0;
}
</style>


