import Vue from 'vue'
import Router from 'vue-router'
import IndexPage from '@/pages/index'
import OrderPage from '@/pages/order'
import HomePage from '@/pages/home'
import LocationPage from '@/pages/location'
import SearchPage from '@/pages/search'
import CategoryPage from '@/pages/category'
import LoginPage from '@/pages/login'
import ShopPage from '@/pages/shop'
import ShopMenuPage from '@/pages/shopmenu'
import ShopCommentPage from '@/pages/shopcomment'
import ShopSellerPage from '@/pages/shopseller'
import ConfirmPage from '@/pages/confirmorder'
import AddressPage from '@/pages/address'
import AddAddressPage from '@/pages/add_address'
import PayPage from '@/pages/pay'
import FinishPayPage from '@/pages/finishpay'
import CommentPage from '@/pages/comment'

Vue.use(Router)

export default new Router({
	mode: 'history',
  routes: [
    {
      path: '/',
      redirect: '/index',
    },
    {
      path: '/index',
      component: IndexPage,
      meta: {'keepAlive': true}
    },
    {
      path: '/order',
      component: OrderPage
    },
    {
      path: '/home',
      component: HomePage
    },
    {
      path: '/location',
      component: LocationPage
    },
    {
      path: '/search',
      component: SearchPage
    },
    {
      path: '/category',
      component: CategoryPage
    },
    {
      path: '/login',
      component: LoginPage
    },
    {
      path: '/shop',
      component: ShopPage,
      children: [
        {
          path: 'menu',
          component: ShopMenuPage
        },
        {
          path: 'comment',
          component: ShopCommentPage
        },
        {
          path: 'seller',
          component: ShopSellerPage
        },
        {
          path: 'middle',
          redirect: '/shop/menu'
        },
        {
          path: '',
          redirect: '/shop/menu'
        },
      ]
    },
    {
      path: '/confirmOrder',
      component: ConfirmPage
    },
    {
      path: '/address',
      component: AddressPage
    },
    {
      path: '/add_address',
      component: AddAddressPage,
      meta: {'keepAlive': true}
    },
    {
      path: '/pay',
      component: PayPage
    },
    {
      path: '/finishPay',
      component: FinishPayPage
    },
    {
      path: '/comment',
      component: CommentPage
    }
  ]
})



