<template>
	<div>
    <div class="app-header">
    	<span>华阳外卖</span>
    </div>
    <div class="guide">
    	<router-link to="/location" class="location">
    		<i class="iconfont icon-location"></i>
    		<span class="address">{{ address }}</span>
    		<i class="iconfont icon-right"></i>
    	</router-link>
    	<router-link to="/search" class="search">
    		<i class="iconfont icon-search"></i>
    		<span class="s-text">搜索店铺</span>
    	</router-link>
    </div>
    <t-tab></t-tab>
    <div class="shopHeader">
    	<span class="line"></span>
    	<h2>附近商家</h2>
    	<span class="line"></span>
    </div>
    <keep-alive>
    	<shop-list shoptype="1"></shop-list>
    </keep-alive>
    <b-tab :currentIndex=0></b-tab>
  </div>
</template>

<script>
import BTab from '../components/bottomtab'
import TTab from "../components/typetab"
import ShopList from "../components/shoplist"
import {mapGetters, mapActions} from 'vuex'

export default {
	components: {
		BTab,
		TTab,
		ShopList
	},
	computed: {
		...mapGetters([
			'address', 'location'
		])
	},
	methods: {
		...mapActions([
			'getLocation'
		]),
	},
	created () {
		if (this.address === '定位中...') {
			this.getLocation();
		};
	},
}
</script>

<style scoped>
.app-header {
	margin: 10px 0;
	text-align: center;
	font-size: 25px;
	font-weight: 700;
}

.guide {
	display: flex;
}

.location {
	display: flex;
	flex: 0 0 33.3333%;
	margin: 0 10px;
	height: 35px;
	border-radius: 10px;
	background: #a6a6a6;
	align-items: center;
	cursor:pointer;
}

.location :first-child {
	padding-left: 8px;
}

.location :last-child {
	padding-right: 8px;
}

.address {
	flex: 1;
	font-size: 15px;
	width: 90px;
	overflow: hidden;
	white-space: nowrap;
	text-overflow: ellipsis;
}

.location > * {
	color: #fff;
}

.search {
	display: flex;
	flex: 1;
	margin-right: 10px;
	height: 35px;
	border-radius: 10px;
	background: #ededed;
	align-items: center;
	cursor:pointer;
	color: #000;
}

.search :first-child {
	padding-left: 8px;
	padding-right: 10px;
}

.shopHeader {
	text-align: center;
  padding-bottom: 10px;
  border-bottom: 1px solid #e4e4e4; /*no*/
}

.shopHeader h2 {
	font-size: 24px;
	display: inline-block;
	margin: 5px;
}

.line {
	vertical-align: middle;
  display: inline-block;
  width: 60px;
  height: 0;
  border-bottom: 1px solid #000; /*no*/
}
</style>


