<template>
	<div class="home">
		<div class="header">
			<span class="back" @click="goback"><i class="iconfont icon-back"></i></span>
			<span class="title">我的</span>
			<span v-if="login" class="logout" @click="logout"><i class="iconfont icon-log-out"></i></span>
		</div>
		<div class="userinfo">
			<label for="file" class="avatar">
				<img v-if="login" :src="user.avatar">
				<img v-else src="http://i.waimai.meituan.com/static/img/default-avatar.png">
				<input id="file" type="file" style="display:none" accept="image/*" @change="uploadImg($event)">
			</label>
			<router-link v-if="!login" to="/login" class="login">登录/注册</router-link>
			<span v-else>{{ user.name }}</span>
		</div>
		<div class="myfunc">
			<div class="type" v-for="item in funcChoices" :key=item.id>
				<div class="typeImg">
					<img :src="item.imgSrc">
				</div>
				<span class="typeName">{{ item.text }}</span>
			</div>
		</div>
		<h3>我的资产</h3>
		<div class="financial">
			<div class="type" v-for="item in financialChoices" :key=item.id>
				<div class="typeImg">
					<img :src="item.imgSrc">
				</div>
				<span class="typeName">{{ item.text }}</span>
			</div>
		</div>
		<h3>更多推荐</h3>
		<div class="more">
			<div class="type" v-for="item in moreChoices" :key=item.id>
				<div class="typeImg">
					<img :src="item.imgSrc">
				</div>
				<span class="typeName">{{ item.text }}</span>
			</div>
		</div>
		<alert-msg :alert_text="alert_text" :alert_show="alert_show" @update="update"></alert-msg>
		<b-tab :currentIndex=2></b-tab>
	</div>
</template>

<script>
import BTab from '../components/bottomtab'
import AlertMsg from '../components/alertmsg'
import {mapGetters, mapMutations, mapActions} from 'vuex'

export default {
	components: {
		BTab, AlertMsg
	},
	data () {
		return {
			funcChoices:[
				{text:"我的收藏", imgSrc:"http://p1.meituan.net/50.0.100/xianfu/9c1388ba5fbb367c1a93996f39c2fba94506.jpg"},
				{text:"我的足迹", imgSrc:"http://p1.meituan.net/50.0.100/xianfu/7ad7da19bfadd5e6081b7606025214254582.jpg"},
				{text:"我的评价", imgSrc:"http://p0.meituan.net/50.0.100/xianfu/5d02f44df0f9f26ea0eca95957824bae4444.jpg"},
				{text:"我的好友", imgSrc:"http://p1.meituan.net/50.0.100/xianfu/bbae84a587711ac12badb9453406ad694851.jpg"},
				{text:"答谢记录", imgSrc:"http://p1.meituan.net/50.0.100/xianfu/5c1bf832376403ca2ab22b8d8748e0fd5479.jpg"},
				{text:"我的地址", imgSrc:"http://p0.meituan.net/50.0.100/xianfu/a813bff1813024b05ff45422deac24bd4276.jpg"}
			],
			financialChoices:[
				{text:"红包", imgSrc:"http://p1.meituan.net/50.0.100/xianfu/a361ce97f9f00f2715bb960a789d925e2315.jpg"},
				{text:"代金券", imgSrc:"http://p0.meituan.net/50.0.100/xianfu/875f13a76045b7f6862a2b7149babec32329.jpg"},
				{text:"钱包", imgSrc:"http://p1.meituan.net/50.0.100/xianfu/2c14b3425c7bf1f3d63d11f47a7ef9ea2230.jpg"},
				{text:"余额", imgSrc:"http://p0.meituan.net/50.0.100/xianfu/7b3e3fb4fc9b45dcda74d7e916f025ea2878.jpg"}
			],
			moreChoices:[
				{text:"邀请有礼", imgSrc:"http://p0.meituan.net/50.0.100/xianfu/cf5ddfcae114ed8d7d147d51064532252477.jpg"},
				{text:"商家入驻", imgSrc:"http://p1.meituan.net/50.0.100/xianfu/55748d5fa531a057258f68d029fe20542466.jpg"},
				{text:"帮助与反馈", imgSrc:"http://p1.meituan.net/50.0.100/xianfu/317aabdd31dfcfa1739149089a2e041a2780.jpg"},
				{text:"在线客服", imgSrc:"http://p0.meituan.net/50.0.100/xianfu/55454d4faaed6ad212b2b8a929edef372425.jpg"}
			],
			loading: false,
			alert_text: '',
			alert_show: false,
		}
	},
	methods: {
		goback () {
			this.$router.go(-1);
		},
		uploadImg (event) {
			if (!this.login) {
				this.$router.push('/login');
			} else {
				let file = event.target.files[0];
				if (file.size > 1024 * 1024 * 2) {
					this.alert_text = "上传失败，只能传2M以内的图片";
					this.alert_show = true;
				} else {
					let form_data = new FormData();
					form_data.append('file', file);
					form_data.append('username', this.user.name);
					if (!this.loading) {
						this.loading = true;
						this.$http.post('http://120.79.182.246:5000/api/uploadavatar', form_data)
							.then( (res) => {
								this.loading = false;
								if ( res.data.code == 0) {
									this.alert_text = res.data.err;
									this.alert_show = true;
								} else {
									this.updateUser(res.data.data)
								}
							})
							
					}
				};
			};
		},
		logout () {
			this.logoutUser();
			this.$router.push('/home');
		},
		update (data) {
			this.alert_show = data;
		},
		...mapMutations({
			updateUser: 'UPDATE_USER'
		}),
		...mapActions([
			'logoutUser'
		])
	},
	computed: {
		...mapGetters([
			'login', 'user', 'cartlist'
		])
	}
}
</script>

<style scoped>
.home {
	position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  overflow-y: auto;
  background: #f4f4f4;
}

.header {
	position: relative;
	padding: 0 5px;
	text-align: center;
	background: #fff;
	line-height: 52px;
}

.back {
	position: absolute;
	left: 10px;
}

.back i {
	font-size: 25px; 
}

.logout {
	position: absolute;
	right: 10px;
}

.logout i {
	font-size: 25px;
}

.title {
	flex: 1;
  font-size: 25px;
  font-weight: 700;
  text-align: center;
}

.userinfo {
	display: flex;
	height: 140px;
	align-items: center;
  background: #fff;
}

.avatar {
	width: 75px;
	height: 75px;
	margin: 0 30px;
	border: 1px solid #333; /*no*/
	border-radius: 50%;
	overflow: hidden;
}

.avatar img {
	width: 100%;
	height: 100%;
}

.login {
	font-size: 24px; 
	color: #000;
}

.userinfo span {
	font-size: 24px;
}

.myfunc{
	margin: 20px 0;
	display: flex;
	flex-wrap: wrap;
	background: #fff;
}

.financial,.more {
	margin-bottom: 20px;
	display: flex;
	flex-wrap: wrap;
	background: #fff;
}

.type {
	width: 25%;
	margin: 18px 0;
	display: flex;
	text-align: center;
	flex-direction: column;	
	justify-content: center;
  align-items: center;
  cursor:pointer;
}

.typeImg {
	height: 45px;
	width: 45px;
}

.typeImg img {
	height: 100%;
	width: 100%;
}

.typeName {
	margin-top: 5px;
	font-size: 18px;
	color: #000;
}

h3 {
	padding: 10px 0 10px 20px;
	font-size: 22px;
	border-bottom: 1px solid #e4e4e4; /*no*/
	background: #fff;
}
</style>


